import React from 'react'
import { ChainId, ETHER, MATIC, Price } from 'tomiswap-sdk'
import { useContext } from 'react'
import { Repeat } from 'react-feather'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { StyledBalanceMaxMini } from './styleds'
import { useActiveWeb3React } from 'hooks'

interface TradePriceProps {
  price: number
  showInverted: boolean
  setShowInverted: (showInverted: boolean) => void,
  showPrice?: boolean,
  quoteCurrency: string,
  baseCurrency: string
}

export default function TradePrice({ price, showInverted, setShowInverted, showPrice, quoteCurrency, baseCurrency }: TradePriceProps) {
  const theme = useContext(ThemeContext)
  const { chainId } = useActiveWeb3React()
  const formattedPrice = showInverted ? price?.toFixed(3) : (1 / price).toFixed(3);
  console.log('price', price)
  const show = showPrice;
  const label = showInverted
    ? `${quoteCurrency} per ${baseCurrency}`
    : `${baseCurrency} per ${quoteCurrency}`

  return (
    <Text
      fontWeight={500}
      fontSize={14}
      color={theme.text2}
      style={{ justifyContent: 'center', alignItems: 'center', display: 'flex' }}
    >
      {show ? (
        <>
          {formattedPrice ?? '-'} {label}
          <StyledBalanceMaxMini onClick={() => setShowInverted(!showInverted)}>
            <Repeat size={14} />
          </StyledBalanceMaxMini>
        </>
      ) : (
        '-'
      )}
    </Text>
  )
}
