import React, { useCallback, useEffect, useState } from 'react'
import styled from 'styled-components/macro'
import { Text } from 'rebass'
import { CloseIcon } from 'theme'
import Column from 'components/Column'
import Modal from 'components/Modal'
import { AutoRow, RowBetween } from 'components/Row'
import { Input as NumericalInput } from 'components/NumericalInput'
import { ButtonError } from 'components/Button'
import { ApprovalState } from 'hooks/useApproveCallback'
import { useGovernanceContract, useTomiTokenContract } from 'hooks/useContract'
import { useActiveWeb3React } from 'hooks'
import { formatEther } from 'ethers/lib/utils'
import Loader from 'components/Loader'

interface ProposalActionSelectorModalProps {
  isOpen: boolean
  onDismiss: () => void
  handleApprove: () => void
  onChangeCost?: (value: string) => void
  amount: string
  approval?: ApprovalState
  isAdmin?: boolean
  currentAmount?: string
}

const ContentWrapper = styled(Column)`
  width: 100%;
  flex: 1 1;
  position: relative;
  padding: 25px;
`
const WrapASection = styled.div`
  margin-top: 15px;
`
const ProposalActionSelectorFlex = styled.div`
  margin-top: 15px;
  display: flex;
  flex-flow: column nowrap;
  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.bg2};
  background-color: ${({ theme }) => theme.bg1};
`

const ProposalActionSelectorContainer = styled.div`
  flex: 1;
  padding: 1rem;
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: 8px;
`

const TextError = styled.p`
  color: ${({ theme }) => theme.red2};
  font-size: 16px;
  // margin-top: -0.2rem;
`
const ConfirmButton = ({
  isFormInvalid,
  handleApprove,
  approval
}: {
  isFormInvalid: boolean
  handleApprove: () => void
  approval?: ApprovalState
}) => {
  return (
    <ButtonError style={{ marginTop: '18px' }} disabled={isFormInvalid} onClick={handleApprove}>
      {approval === ApprovalState.APPROVED && 'Confirm'}
      {approval === ApprovalState.PENDING && (
        <>
          <AutoRow gap="6px" justify="center">
            Pending <Loader stroke="white" />
          </AutoRow>
        </>
      )}
      {approval === ApprovalState.NOT_APPROVED && 'Approve'}
      {approval === ApprovalState.UNKNOWN && 'Confirm'}
    </ButtonError>
  )
}

export function ProposalCostModal({
  isOpen,
  onDismiss,
  onChangeCost,
  amount,
  isAdmin,
  handleApprove,
  approval,
  currentAmount
}: ProposalActionSelectorModalProps) {
  const onChangeCostProposal = useCallback(
    (value: string) => {
      if (onChangeCost) onChangeCost(value)
    },
    [onChangeCost]
  )
  const [staticInfo, setStaticInfo] = useState({ balance: '0' })

  const tomiTokenContract = useTomiTokenContract()
  const { account, chainId } = useActiveWeb3React()
  const getStaticInfo = useCallback(async () => {
    try {
      if (tomiTokenContract && chainId) {
        const balanceTomi = await tomiTokenContract?.functions.balanceOf(account)
        setStaticInfo({
          balance: formatEther(balanceTomi[0].toString())
        })
      }
    } catch (e) {
      console.log('something went wrong: ', e)
    }
  }, [tomiTokenContract])

  useEffect(() => {
    getStaticInfo()
  }, [tomiTokenContract])

  const isFormInvalid = Number(amount) < Number(currentAmount) || approval === ApprovalState.PENDING
  const notEnough = amount ? Number(amount) < Number(currentAmount) : false
  const isExceed = (amount && staticInfo.balance) && !isAdmin ? Number(amount) > Number(staticInfo.balance) : false
  const border = isExceed || notEnough || amount == '0' ? '1px solid red' : 'none'
  return (
    <Modal isOpen={isOpen} onDismiss={onDismiss}>
      <ContentWrapper>
        <RowBetween>
          <Text fontWeight={500} fontSize={18}>
            Payment
          </Text>
          <CloseIcon onClick={onDismiss} />
        </RowBetween>
        <WrapASection>
          <Text fontWeight={500} fontSize={16}>
            Pay TOMI: {currentAmount}
          </Text>
        </WrapASection>
        <ProposalActionSelectorFlex>
          <ProposalActionSelectorContainer
            style={{ border: `1px solid ${notEnough || isExceed || amount == '0' ? 'red' : '#e01286'}` }}
          >
            <NumericalInput
              fontSize={'18px'}
              className="token-amount-input"
              align={'left'}
              value={amount}
              border={border}
              onUserInput={onChangeCostProposal}
            />
          </ProposalActionSelectorContainer>
        </ProposalActionSelectorFlex>
        {amount == '0' ? (
          <TextError>Please input an amount greater than {currentAmount}</TextError>
        ) : isExceed ? (
          <TextError>Insufficient balance</TextError>
        ) : notEnough ? (
          <TextError>Please enter an amount greater than {currentAmount}</TextError>
        ) : (
          <></>
        )}
        <WrapASection>
          <ConfirmButton isFormInvalid={isFormInvalid} handleApprove={handleApprove} approval={approval} />
        </WrapASection>
      </ContentWrapper>
    </Modal>
  )
}
