function Balance({ balance }: { balance: CurrencyAmount }) {
  const balanceFormat = Number(balance.toFixed(balance.currency.decimals > 8 ? 8 : balance.currency.decimals)).toFixed(
    balance.currency.decimals > 8 ? 8 : balance.currency.decimals
  )
  // const showBalance = balanceFormat.split('.')[0].length > 8 ? `${balanceFormat.substring(0, 6)}...` : balanceFormat

  return (
    <StyledBalanceText fontWeight={600} title={balance.toExact()}>
      {balanceFormat}
    </StyledBalanceText>
  )
}

