import React, { useEffect, useState } from 'react'

import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { AutoRow, RowBetween, RowFixed } from '../Row'
import { TYPE } from '../../theme'
import { X } from 'react-feather'
import { ButtonConfirmed, ButtonEmpty, ButtonPrimary } from '../Button'
import { escapeRegExp } from '../../utils'
import { formatDecimal } from 'utils/util'
import { isMobile } from 'react-device-detect'
import Loader from 'components/Loader'

interface RedeemModalProps {
  isOpen: boolean
  onDismiss: () => void
  onHandle: (e: any) => void
  balance: string
  type: string
  loading: boolean
}
const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 24px;
`
const StyledClosed = styled(X)`
  :hover {
    cursor: pointer;
  }
`
const TextError = styled.p`
  color: ${({ theme }) => theme.red2};
  margin-top: 0.5rem;
  font-size: 16px;
  margin-bottom: 0.5rem;
`
const InputDiv = styled.div`
  background: #212121;
  width: 100%;
  height: 51px;
  /* strocke 1 */

  border: 1px solid #e01286;
  box-sizing: border-box;
  border-radius: 8px;
  color: #fffffff;
  #input-box {
    color: #ffffff;
  }
`
const InputBox = styled.input`
  background-color: transparent;
  width: ${isMobile ? '64%' : '74%'};
  height: 100%;
  border: none;
  font-size: 16px;
  color: #fffffff !important;
  padding-left: 1rem;
  :focus {
    outline: none;
  }
  ::-webkit-search-decoration {
    -webkit-appearance: none;
  }

  [type='number'] {
    -moz-appearance: textfield;
  }
`
const MaxButton = styled.button`
  align-items: center;
  letter-spacing: 0.75px;
  background-color: transparent;
  left: ${isMobile ? 'calc(50% - 42px / 2 + 109.5px)' : 'calc(50% - 42px / 2 + 151.5px)'};
  border: none;
  color: #e01286;
  cursor: pointer;
`
const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`) // match escaped "." characters via in a non-capturing group

export default function PositionModal({ isOpen, onDismiss, onHandle, balance, type, loading }: RedeemModalProps) {
  const [amount, setAmount] = useState('')
  const [notInput, setNotInput] = useState(true)
  const enforcer = (nextUserInput: string) => {
    if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
      if (nextUserInput.includes('.')) {
        const arr = nextUserInput.split('.')
        const decimal = arr[1]
        if (decimal.length > 8) return
      }
      if (notInput) setNotInput(false)
      setAmount(nextUserInput)
    }
  }
  const setMax = () => {
    setAmount(
      formatDecimal(balance)
        .toString()
        .replace(/,/g, '')
    )
  }
  useEffect(() => {
    setAmount('')
  }, [isOpen])
  const handleDismiss = () => {
    setAmount('')
    setNotInput(true)
    onDismiss()
  }

  const isError = amount ? Number(amount) > Number(balance) : false
  const isZero = amount ? Number(amount) == 0 : false
  const isEmpty = !amount && !notInput ? true : false
  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <ContentWrapper gap="lg">
        <AutoColumn gap="lg" justify="center">
          <RowBetween>
            <TYPE.mediumHeader fontSize={isMobile ? 16 : 18}>
              {type == 'DEPOSIT' ? 'Deposit TOMI Tokens' : 'Withdraw TOMI Tokens'}
            </TYPE.mediumHeader>
            <StyledClosed stroke="white" onClick={handleDismiss} />
          </RowBetween>
          <RowBetween>
            <InputDiv style={{ border: `1px solid ${isError || isZero ? 'red' : '#e01286'}` }}>
              <InputBox
                id="input-box"
                inputMode="decimal"
                type="text"
                pattern="^[0-9]*[.,]?[0-9]*$"
                placeholder={'Amount'}
                value={amount}
                minLength={1}
                maxLength={79}
                onChange={event => {
                  enforcer(event.target.value.replace(/,/g, ''))
                }}
              />
              <span style={{ position: 'absolute', marginTop: '13px', marginRight: '1rem' }}>TOMI</span>
              <MaxButton style={{ position: 'absolute', marginTop: '13px', fontSize: '16px' }} onClick={setMax}>
                MAX
              </MaxButton>
              {isError && <TextError>Insufficient balance</TextError>}
              {isZero && <TextError>Please enter an amount greater than 0</TextError>}
            </InputDiv>
          </RowBetween>
          <RowBetween style={{ marginTop: '0.4rem' }}>
            {type == 'DEPOSIT' ? 'Wallet Balance' : 'TOMI Staked'}: {formatDecimal(balance)}
          </RowBetween>
          <RowBetween>
            <ButtonEmpty style={{ width: '46%', color: 'white', height: '52px' }} onClick={() => handleDismiss()}>
              <TYPE.mediumHeader fontSize="16px" color="white">{`Exit`}</TYPE.mediumHeader>
            </ButtonEmpty>
            <ButtonPrimary
              style={{ width: '46%' }}
              disabled={isError || amount == '' || isZero || loading}
              onClick={() => onHandle(amount)}
            >
              {loading ? (
                <AutoRow gap="6px" justify="center">
                  Confirm <Loader stroke="white" />
                </AutoRow>
              ) : (
                <>Confirm</>
              )}
            </ButtonPrimary>
          </RowBetween>
        </AutoColumn>
      </ContentWrapper>
    </Modal>
  )
}
