import { Trade } from 'tomiswap-sdk'
import React, { Fragment, memo, useContext } from 'react'
import { ChevronRight } from 'react-feather'
import { Flex } from 'rebass'
import styled, { ThemeContext } from 'styled-components'
import { TYPE } from '../../theme'
import { unwrappedToken } from 'utils/wrappedCurrency'
import CurrencyLogo from '../CurrencyLogo'

const RouteBorder = styled.div`
  padding: 10px 50px;
  border-radius: 8px;
  border: ${({ theme }) => `1px solid ${theme.bg3}`};
  display: flex;
  justify-content: center;
  align-items: center;
`

export default memo(function SwapRoute({ trade }: { trade: Trade }) {
  const theme = useContext(ThemeContext)
  return (
    <Flex marginTop="20px" flexWrap="wrap" width="100%" justifyContent="center" alignItems="center">
      <RouteBorder>
        {trade.route.path.map((token, i, path) => {
          const isLastItem: boolean = i === path.length - 1
          const currency = unwrappedToken(token)
          return (
            <Fragment key={i}>
              <Flex
                marginLeft="15px"
                marginRight="15px"
                padding="6px"
                justifyContent="center"
                backgroundColor={theme.text6}
                alignItems="center"
              >
                <CurrencyLogo currency={currency} size={'24px'} />
                <TYPE.black margin={'0 0 0 15px !important'} fontSize={16} fontWeight={500} color={theme.text1}>
                  {currency.symbol}
                </TYPE.black>
              </Flex>
              {isLastItem ? null : <ChevronRight color={theme.text1} />}
            </Fragment>
          )
        })}
      </RouteBorder>
    </Flex>
  )
})
