import React, { useState } from 'react'

import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { RowBetween, RowFixed } from '../Row'
import { TYPE } from '../../theme'
import { X } from 'react-feather'
import { ButtonConfirmed, ButtonEmpty, ButtonPrimary } from '../Button'
import { escapeRegExp } from '../../utils'
import { formatDecimal } from 'utils/util'

interface DepositModalProps {
  isOpen: boolean
  onDismiss: () => void
  onHandle: (e: any) => void
  balance: string
}
const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 24px;
`
const StyledClosed = styled(X)`
  :hover {
    cursor: pointer;
  }
`
const TextError = styled.p`
  color: ${({ theme }) => theme.red2};
  margin-top: -0.2rem;
  font-size: 16px;
`
const InputDiv = styled.div`
  background: #212121;
  width: 100%;
  height: 51px;
  /* strocke 1 */

  border: 1px solid #e01286;
  box-sizing: border-box;
  border-radius: 8px;
  color: #fffffff;
  #input-box {
    color: #ffffff;
  }
`
const InputBox = styled.input`
  background-color: transparent;
  width: 73%;
  height: 100%;
  border: none;
  color: #fffffff !important;
  padding-left: 1rem;
  :focus {
    outline: none;
  }
`
const MaxButton = styled.button`
  align-items: center;
  letter-spacing: 0.75px;
  background-color: transparent;
  left: calc(50% - 42px / 2 + 151.5px);
  border: none;
  color: #e01286;
  cursor: pointer;
`
const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`) // match escaped "." characters via in a non-capturing group

export default function DepositModal({ isOpen, onDismiss, onHandle, balance }: DepositModalProps) {
  const [amount, setAmount] = useState('')
  const [notInput, setNotInput] = useState(true)
  const enforcer = (nextUserInput: string) => {
    if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
      if (nextUserInput.includes('.')) {
        const arr = nextUserInput.split('.')
        const decimal = arr[1]
        if (decimal.length > 8) return
      }
      if (notInput) setNotInput(false)
      setAmount(nextUserInput)
    }
  }
  const setMax = () => {
    setAmount(
      formatDecimal(balance)
        .toString()
        .replace(',', '')
    )
  }
  const handleDismiss = () => {
    setAmount('')
    setNotInput(true)
    onDismiss()
  }
  const isError = amount ? Number(amount) > Number(balance) : false
  const isZero = amount ? amount == '0' : false
  const isEmpty = !amount && !notInput ? true : false

  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <ContentWrapper gap="lg">
        <AutoColumn gap="lg" justify="center">
          <RowBetween>
            <TYPE.mediumHeader fontWeight={400}>Collateralize</TYPE.mediumHeader>
            <StyledClosed stroke="black" onClick={onDismiss} />
          </RowBetween>
          <RowBetween>
            <InputDiv>
              <InputBox
                id="input-box"
                inputMode="decimal"
                type="text"
                pattern="^[0-9]*[.,]?[0-9]*$"
                placeholder={'Amount'}
                value={amount}
                minLength={1}
                maxLength={79}
                onChange={event => {
                  enforcer(event.target.value.replace(/,/g, '.'))
                }}
              />
              <span style={{ position: 'absolute', marginTop: '13px', marginRight: '1rem' }}>TOMI</span>
              <MaxButton style={{ position: 'absolute', marginTop: '14px' }} onClick={setMax}>
                MAX
              </MaxButton>
              {isError && <TextError>Insufficient balance</TextError>}
              {isZero && <TextError>Please enter an amount greater than 0</TextError>}
              {isEmpty && <TextError>Please enter an amount</TextError>}
            </InputDiv>
          </RowBetween>
          <RowFixed></RowFixed>
          <RowBetween>Wallet balance: {formatDecimal(balance)}</RowBetween>
          <RowBetween>
            <ButtonEmpty style={{ width: '46%', color: 'white' }} onClick={() => onDismiss()}>
              <TYPE.mediumHeader color="black">{`Exit`}</TYPE.mediumHeader>
            </ButtonEmpty>
            <ButtonConfirmed
              style={{ width: '46%' }}
              disabled={isError || amount == '' || isZero}
              onClick={() => onHandle(amount)}
            >
              <TYPE.mediumHeader color="black">{`Confirm`}</TYPE.mediumHeader>
            </ButtonConfirmed>
          </RowBetween>
        </AutoColumn>
      </ContentWrapper>
    </Modal>
  )
}
