import { Fraction, Trade, TradeType } from 'tomiswap-sdk'
import React, { useContext, useMemo, useState } from 'react'
import { Repeat } from 'react-feather'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { Field } from '../../state/swap/actions'
import { TYPE } from '../../theme'
import {
  computeSlippageAdjustedAmounts,
  computeTradePriceBreakdown,
  formatExecutionPrice,
  warningSeverity
} from '../../utils/prices'
import { ButtonError } from '../Button'
import { AutoColumn } from '../Column'
import QuestionHelper from '../QuestionHelper'
import { AutoRow, RowBetween, RowFixed } from '../Row'
import FormattedPriceImpact from './FormattedPriceImpact'
import { StyledBalanceMaxMini, SwapCallbackError } from './styleds'
import { MIN_CURRENCY } from '../../constants'
import { ConfigKey, useTomiConfig } from '../../hooks/useTomiConfig'
import { useWeb3React } from '@web3-react/core'
import { getTextCoin } from './AdvancedSwapDetails'
import { useChainId } from 'hooks'

export default function SwapModalFooter({
  trade,
  onConfirm,
  allowedSlippage,
  swapErrorMessage,
  disabledConfirm
}: {
  trade: Trade
  allowedSlippage: number
  onConfirm: () => void
  swapErrorMessage: string | undefined
  disabledConfirm: boolean
}) {
  const [showInverted, setShowInverted] = useState<boolean>(false)
  const theme = useContext(ThemeContext)
  const slippageAdjustedAmounts = useMemo(() => computeSlippageAdjustedAmounts(trade, allowedSlippage), [
    allowedSlippage,
    trade
  ])
  const swapProviderFee = useTomiConfig(ConfigKey.providerSwapFeeKey)
  const { priceImpactWithoutFee, realizedLPFee } = useMemo(() => computeTradePriceBreakdown(trade, swapProviderFee), [
    trade,
    swapProviderFee
  ])
  const severity = warningSeverity(priceImpactWithoutFee)

  const outputDecimals = trade.outputAmount.currency.decimals
  const inputDecimals = trade.inputAmount.currency.decimals
  const { chainId } = useChainId()
  return (
    <>
      <AutoColumn gap="0px">
        <RowBetween align="center">
          <Text fontSize={16} fontWeight={600}>
            Price
          </Text>
          <Text
            fontWeight={600}
            fontSize={16}
            style={{
              justifyContent: 'center',
              alignItems: 'center',
              display: 'flex',
              textAlign: 'right',
              paddingLeft: '10px'
            }}
          >
            {formatExecutionPrice(trade, showInverted, chainId)}
            <StyledBalanceMaxMini isConfirmSwap={true} onClick={() => setShowInverted(!showInverted)}>
              <Repeat size={14} />
            </StyledBalanceMaxMini>
          </Text>
        </RowBetween>

        <RowBetween mt="12px">
          <RowFixed>
            <TYPE.black fontSize={16} fontWeight={600}>
              {trade.tradeType === TradeType.EXACT_INPUT ? 'Minimum received' : 'Maximum sold'}
            </TYPE.black>
            {/*<QuestionHelper text="Your transaction will revert if there is a large, unfavorable price movement before it is confirmed." />*/}
          </RowFixed>
          <RowFixed>
            <TYPE.black fontSize={16} fontWeight={600}>
              {trade.tradeType === TradeType.EXACT_INPUT
                ? slippageAdjustedAmounts[Field.OUTPUT]?.toFixed(outputDecimals >= 8 ? 8 : outputDecimals) ?? '-'
                : slippageAdjustedAmounts[Field.INPUT]?.toFixed(inputDecimals >= 8 ? 8 : inputDecimals) ?? '-'}
            </TYPE.black>
            <TYPE.black fontSize={16} fontWeight={600} marginLeft={'4px'}>
              {trade.tradeType === TradeType.EXACT_INPUT
                ? getTextCoin(trade.outputAmount.currency.symbol, chainId)
                : getTextCoin(trade.inputAmount.currency.symbol, chainId)}
            </TYPE.black>
          </RowFixed>
        </RowBetween>
        <RowBetween mt="12px">
          <RowFixed>
            <TYPE.black fontSize={16} fontWeight={600}>
              Price Impact
            </TYPE.black>
            {/*<QuestionHelper text="The difference between the market price and your price due to trade size." />*/}
          </RowFixed>
          <FormattedPriceImpact priceImpact={priceImpactWithoutFee} />
        </RowBetween>
        <RowBetween mt="12px">
          <RowFixed>
            <TYPE.black fontSize={16} fontWeight={600}>
              Liquidity Provider Fee
            </TYPE.black>
            {/*<QuestionHelper text="A portion of each trade (0.30%) goes to liquidity providers as a protocol incentive." />*/}
          </RowFixed>
          <TYPE.black fontSize={16} fontWeight={600}>
            {realizedLPFee
              ? realizedLPFee.lessThan(MIN_CURRENCY)
                ? '<0.01' + ' ' + getTextCoin(trade.inputAmount.currency.symbol, chainId)
                : realizedLPFee?.toFixed(6) + ' ' + getTextCoin(trade.inputAmount.currency.symbol, chainId)
              : '-'}
          </TYPE.black>
        </RowBetween>
      </AutoColumn>

      <AutoRow>
        <ButtonError
          onClick={onConfirm}
          disabled={disabledConfirm}
          error={severity > 3}
          style={{ margin: '40px 0 0 0' }}
          id="confirm-swap-or-send"
        >
          <Text fontSize={16} fontWeight={600}>
          Swap  
          </Text>
        </ButtonError>
        {swapErrorMessage ? <SwapCallbackError error={swapErrorMessage} /> : null}
      </AutoRow>
    </>
  )
}
