import { parseEther } from 'ethers/lib/utils';
import { BALLOT_CONFIG } from './../pages/CreateProposal/ProposalActionSelector'
import { BigNumber } from 'ethers'
import { BALLOT_REVENUE } from 'pages/CreateProposal/ProposalActionSelector'

export const generateUserStatus = (
  ended: boolean,
  voted: boolean | null | undefined,
  audited: boolean | null | undefined,
  voteIndex: BigNumber,
  userAccount: string | null | undefined,
  proposal: string,
  minted: boolean | null | undefined,
  weight: BigNumber,
  result: boolean,
  ballotType: string,
  participated?: boolean | null,
  totalReward?: BigNumber | null,
  currentTimeStamp?: number,
  endTime?: BigNumber,
  executionTime?: BigNumber
): string => {
  if (ballotType == BALLOT_REVENUE) {
    if (endTime?.lt(BigNumber.from(currentTimeStamp))) {
      if (!participated && userAccount != proposal) return ''
      if (audited) {
        if (minted || totalReward?.eq(0)) return 'DONE'
        // else return 'CLAIM'
        return 'DONE'
      } else {
        if (participated && userAccount != proposal) {
          if (minted) return 'DONE'
          else {
            console.log('minted', minted)
            return 'CLAIM'
          }
        } else {
          if (userAccount == proposal) return 'DONE'
        }
      }
    } else {
      if (participated) {
        return 'YOU JOINED'
      } else {
        return 'YOU CAN JOIN'
      }
    }
  }
  if (ballotType == BALLOT_CONFIG) {
    const isProposal = proposal == userAccount
    const isWin = result
    if (!userAccount) return ''

    // pass freeze time
    if (executionTime?.lt(BigNumber.from(currentTimeStamp))) {
      if (isProposal) {
        if (audited) return 'DONE'
        if (isWin) return 'EXECUTE'
        return ''
      } else {
        if (weight.eq(0)) return ''
        if (minted || totalReward?.eq(0)) return 'DONE'
        return 'CLAIM'
      }
    } else {
      // pass voting time
      if (endTime?.lt(BigNumber.from(currentTimeStamp))) {
        if (isProposal) return 'WAITING'
        else {
          if (weight.eq(0)) return 'DONE'
          return 'WAITING'
        }
      } else {
        if (voted) {
          if (voteIndex.eq(1)) return 'YOU AFFIRMATIVE'
          else return 'YOU NEGATIVE'
        } else {
          return 'YOU CAN VOTE'
        }
      }
    }
  } else {
    const isProposal = proposal == userAccount
    const isWin = result
    if (!userAccount) return ''

    // pass freeze time
    if (endTime?.lt(BigNumber.from(currentTimeStamp))) {
      if (isProposal) {
        if (totalReward?.eq(0)) return 'DONE'
        // if (!minted) return 'CLAIM'
        return 'DONE'
      } else {
        if (totalReward?.eq(0)) {
          if (weight.eq(0)) return ''
          return 'DONE'
        } else {
          if (minted) return 'DONE'
          if (weight.eq(0) || totalReward?.eq(0)) return ''
          return 'CLAIM'
        }
      }
    } else {
      if (voted) {
        if (voteIndex.eq(1)) return 'YOU AFFIRMATIVE'
        else return 'YOU NEGATIVE'
      } else {
        return 'YOU CAN VOTE'
      }
    }
  }
}

export const checkExcutable = (
  ended: boolean,
  audited: boolean,
  result: boolean,
  proposer: string,
  account: string | null | undefined,
  ballotType: string,
  currentTimeStamp?: number,
  endTime?: BigNumber,
  executionTime?: BigNumber
) => {
  if (ballotType == BALLOT_REVENUE) return false
  if (
    executionTime?.gt(BigNumber.from(currentTimeStamp)) ||
    (executionTime?.lt(BigNumber.from(currentTimeStamp)) && audited)
  )
    return false
  const win = result
  const isProposer = proposer == account
  if (!win || !isProposer) return false
  return true
}
