interface ProposalActionSelectorModalProps {
  isOpen: boolean
  ballotType: string
  onDismiss: () => void
  votingNameList: Array<any>
  onProposalActionSelect?: (proposalAction: any) => void
}

const ContentWrapper = styled(Column)`
  width: 100%;
  flex: 1 1;
  position: relative;
`
const ActionSelectorHeader = styled.div`
  font-size: 14px;
  font-weight: 500;
  color: ${({ theme }) => theme.text2};
`

const ActionDropdown = styled(ButtonDropdown)`
  padding: 0px;
  background-color: transparent;
  color: ${({ theme }) => theme.text1};
  font-size: 18px;
  text-align: left;
  :hover,
  :active,
  :focus {
    outline: 0px;
    box-shadow: none;
    background-color: transparent;
  }
`

const ProposalActionSelectorFlex = styled.div`
  margin-top: 10px;
  display: flex;
  flex-flow: column nowrap;
  border-radius: 8px;
  border: 1px solid #e01286;
  background-color: #232428;
`

const ProposalActionSelectorContainer = styled.div`
  flex: 1;
  padding: 1rem;
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: 10px;
`
const CreateProposalModal = styled(Modal)`
  border-radius: 8px;
`
const MenuItemVote = styled(MenuItem)`
  background: #232428;
  :hover {
    color: #ffffff;
    background-color: #453922;
  }
`
export const ProposalActionSelector = ({
  className,
  onClick,
  proposalAction,
  ballotType
}: {
  className?: string
  onClick: () => void
  ballotType: string
  proposalAction: any
}) => {
  return (
    <ProposalActionSelectorFlex>
      <ProposalActionSelectorContainer className={className}>
        <ActionDropdown
          style={{
            cursor: ballotType == BALLOT_REVENUE ? 'no-drop' : 'pointer'
          }}
          onClick={onClick}
        >
          {(PROPOSALS as any)[proposalAction].name}
        </ActionDropdown>
      </ProposalActionSelectorContainer>
    </ProposalActionSelectorFlex>
  )
}

export function ProposalActionSelectorModal({
  isOpen,
  onDismiss,
  onProposalActionSelect,
  votingNameList,
  ballotType
}: ProposalActionSelectorModalProps) {
  const handleProposalActionSelect = useCallback(
    (proposalAction: any) => {
      if (onProposalActionSelect) onProposalActionSelect(proposalAction)
      onDismiss()
    },
    [onDismiss, onProposalActionSelect]
  )
  const isDisable = (name: any): boolean => votingNameList.includes(name)
  return (
    <CreateProposalModal
      isOpen={isOpen}
      onDismiss={onDismiss}
      borderRadius={16}
      marginTop={1}
      border={'1px solid #e01286'}
    >
      <ContentWrapper style={{ background: '#232428' }}>
        <PaddedColumn gap="16px">
          <RowBetween>
            <Text fontWeight={500} fontSize={18} style={{ marginLeft: '-12px' }}>
              Select an action
            </Text>
            <CloseIcon onClick={onDismiss} />
          </RowBetween>
        </PaddedColumn>
        <Separator />
        {ballotType == BALLOT_REVENUE ? (
          <>
            <MenuItemVote onClick={() => handleProposalActionSelect((PROPOSALS as any).REVENUE.id)}>
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {PROPOSALS.REVENUE.name}
                </Text>
              </Column>
            </MenuItemVote>
          </>
        ) : (
          <>
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).PROPOSAL_TGAS_AMOUNT.id)}
              onClick={() => handleProposalActionSelect(ProposalAction.PROPOSAL_TGAS_AMOUNT)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {ProposalShowAction[ProposalAction.PROPOSAL_TGAS_AMOUNT]}
                </Text>
              </Column>
            </MenuItemVote>
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).VOTE_DURATION.id)}
              onClick={() => handleProposalActionSelect(ProposalAction.VOTE_DURATION)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {ProposalShowAction[ProposalAction.VOTE_DURATION]}
                </Text>
              </Column>
            </MenuItemVote>
            {/* <MenuItemVote
              disabled={isDisable((PROPOSALS as any).VOTE_REWARD_PERCENT.id)}
              onClick={() => handleProposalActionSelect(ProposalAction.VOTE_REWARD_PERCENT)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {ProposalShowAction[ProposalAction.VOTE_REWARD_PERCENT]}
                </Text>
              </Column>
            </MenuItemVote> */}
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).SWAP_FEE_PERCENT.id)}
              onClick={() => handleProposalActionSelect(ProposalAction.SWAP_FEE_PERCENT)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {ProposalShowAction[ProposalAction.SWAP_FEE_PERCENT]}
                </Text>
              </Column>
            </MenuItemVote>
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).FEE_LP_REWARD_PERCENT.id)}
              onClick={() => handleProposalActionSelect((PROPOSALS as any).FEE_LP_REWARD_PERCENT.id)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {PROPOSALS.FEE_LP_REWARD_PERCENT.name}
                </Text>
              </Column>
            </MenuItemVote>
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).FEE_FUNDME_REWARD_PERCENT.id)}
              onClick={() => handleProposalActionSelect((PROPOSALS as any).FEE_FUNDME_REWARD_PERCENT.id)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {PROPOSALS.FEE_FUNDME_REWARD_PERCENT.name}
                </Text>
              </Column>
            </MenuItemVote>
            <MenuItemVote
              disabled={isDisable((PROPOSALS as any).FEE_LOTTERY_REWARD_PERCENT.id)}
              onClick={() => handleProposalActionSelect((PROPOSALS as any).FEE_LOTTERY_REWARD_PERCENT.id)}
            >
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {PROPOSALS.FEE_LOTTERY_REWARD_PERCENT.name}
                </Text>
              </Column>
            </MenuItemVote>
            <MenuItemVote onClick={() => handleProposalActionSelect((PROPOSALS as any).SNAPSHOT.id)}>
              <Column>
                <Text fontSize={18} fontWeight={500}>
                  {PROPOSALS.SNAPSHOT.name}
                </Text>
              </Column>
            </MenuItemVote>
          </>
        )}
      </ContentWrapper>
    </CreateProposalModal>
  )
}
