import { TokenAmount, Pair, Currency } from 'tomiswap-sdk'
import { useMemo } from 'react'
// import { abi as IUniswapV2PairABI } from '@uniswap/v2-core/build/IUniswapV2Pair.json'
import IUniswapV2PairABI from 'constants/abis/tomi-pair.json'
import { ZERO_ADDRESS } from '../constants'
import { Interface } from '@ethersproject/abi'
import { useActiveWeb3React } from '../hooks'
import { useMultipleContractSingleData, useSingleContractMultipleData } from '../state/multicall/hooks'
import { wrappedCurrency } from '../utils/wrappedCurrency'
import { useFactoryContract } from 'hooks/useContract'

const PAIR_INTERFACE = new Interface(IUniswapV2PairABI)

export enum PairState {
  LOADING,
  NOT_EXISTS,
  EXISTS,
  INVALID
}

export function usePairs(currencies: [Currency | undefined, Currency | undefined][]): [PairState, Pair | null][] {
  const { chainId } = useActiveWeb3React()

  const tokens = useMemo(
    () =>
      currencies.map(([currencyA, currencyB]) => [
        wrappedCurrency(currencyA, chainId),
        wrappedCurrency(currencyB, chainId)
      ]),
    [chainId, currencies]
  )

  const [tokenAddresses, fillValueAddresses] = useMemo(() => {
    const dataTokens: any = []
    const dataValueAddresses: any = []
    tokens.map(([tokenA, tokenB], index) => {
      if (tokenA && tokenB && !tokenA.equals(tokenB)) {
        dataTokens.push([tokenA.address, tokenB.address])
        dataValueAddresses.push(index)
      } else {
        dataValueAddresses.push(undefined)
      }
    })
    return [dataTokens, dataValueAddresses]
  }, [tokens])

  const factory = useFactoryContract()
  const dataPairAddresses = useSingleContractMultipleData(factory, 'getPair', tokenAddresses)
  const pairAddresses = useMemo(() => {
    const data = dataPairAddresses.map(({ result }) => (result?.[0] === ZERO_ADDRESS ? undefined : result?.[0]))
    let index = 0
    return [...fillValueAddresses].map(v => {
      if (v >= 0) {
        const add = data[index] || undefined
        index = index + 1
        return add
      }
      return undefined
    })
  }, [dataPairAddresses, fillValueAddresses])
  const results = useMultipleContractSingleData(pairAddresses, PAIR_INTERFACE, 'getReserves')
  return useMemo(() => {
    return results.map((result, i) => {
      const { result: reserves, loading } = result
      const tokenA = tokens[i][0]
      const tokenB = tokens[i][1]

      if (loading) return [PairState.LOADING, null]
      if (!tokenA || !tokenB || tokenA.equals(tokenB)) return [PairState.INVALID, null]
      if (!reserves) return [PairState.NOT_EXISTS, null]
      const { _reserve0, _reserve1 } = reserves
      const [token0, token1] = tokenA.sortsBefore(tokenB) ? [tokenA, tokenB] : [tokenB, tokenA]
      return [
        PairState.EXISTS,
        new Pair(new TokenAmount(token0, _reserve0.toString()), new TokenAmount(token1, _reserve1.toString()))
      ]
    })
  }, [results, tokens])
}

export function usePair(tokenA?: Currency, tokenB?: Currency): [PairState, Pair | null] {
  return usePairs([[tokenA, tokenB]])[0]
}
