// returns null on errors
const defaultProvider = {
  [ChainId.MAINNET]: ethers.getDefaultProvider(`${process.env.REACT_APP_NETWORK_URL}`),
  [ChainId.RINKEBY]: ethers.getDefaultProvider('https://eth-rinkeby.alchemyapi.io/v2/lIX3oOHsGtsp7MEddXOZNfjIq_SUc7Lt'),
  [ChainId.MUMBAI]: ethers.getDefaultProvider('https://matic-mumbai.chainstacklabs.com'),
  [ChainId.POLYGON]: ethers.getDefaultProvider('https://polygon-rpc.com'),
  [ChainId.GÃ–RLI]: ethers.getDefaultProvider('https://eth-rinkeby.alchemyapi.io/v2/lIX3oOHsGtsp7MEddXOZNfjIq_SUc7Lt'),
  [ChainId.KOVAN]: ethers.getDefaultProvider('https://matic-mumbai.chainstacklabs.com'),
  [ChainId.ROPSTEN]: ethers.getDefaultProvider('https://eth-rinkeby.alchemyapi.io/v2/lIX3oOHsGtsp7MEddXOZNfjIq_SUc7Lt'),
  [ChainId.BINANCE]: ethers.getDefaultProvider('https://nodes.pancakeswap.com/')
}

function useContract(address: string | undefined, ABI: any, withSignerIfPossible = true): Contract | null {
  const { library, account } = useActiveWeb3React()
  const { chainId } = useChainId()
  return useMemo(() => {
    const finalLibrary = defaultProvider[chainId]
    if (!address || !ABI) return null
    if (!library) {
      const contract = new ethers.Contract(address, ABI, finalLibrary)
      if (contract) return contract
      return null
    }
    try {
      return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined)
    } catch (error) {
      console.error('Failed to get contract', error)
      return null
    }
  }, [address, ABI, library, withSignerIfPossible, account])
}
