const NETWORK_LABELS: { [chainId in ChainId]?: string } = {
  [ChainId.RINKEBY]: 'Rinkeby',
  [ChainId.ROPSTEN]: 'Ropsten',
  [ChainId.GÃ–RLI]: 'GÃ¶rli',
  [ChainId.KOVAN]: 'Kovan',
  [ChainId.MAINNET]: 'Ethereum',
  [ChainId.POLYGON]: 'Polygon',
  [ChainId.MUMBAI]: 'Mumbai',
  [ChainId.BINANCE]: 'Binance'
}

export default function Header() {
  const { t } = useTranslation()
  const isMainnet = process.env.REACT_APP_IS_MAINNET == '1' ? true : false
  const { account, library } = useActiveWeb3React()
  const { chainId } = useChainId()
  const coinName = chainId && ![56].includes(Number(chainId)) ? 'MATIC' : 'BNB'
  const userEthBalance = useETHBalances(account ? [account] : [])?.[account ?? '']
  console.log('userEthBalance', userEthBalance, account, chainId)
  // const [isDark] = useDarkModeManager()
  const [darkMode, toggleDarkMode] = useDarkModeManager()
  const [changeNetworkIsOpen, setChangeNetworkIsOpen] = useState(false)

  const toggleClaimModal = useToggleSelfClaimModal()

  const availableClaim: boolean = useUserHasAvailableClaim(account)

  const { claimTxn } = useUserHasSubmittedClaim(account ?? undefined)

  const aggregateBalance: TokenAmount | undefined = useAggregateUniBalance()
  const toggleModal = () => {
    setChangeNetworkIsOpen(!changeNetworkIsOpen)
  }
  const [showUniBalanceModal, setShowUniBalanceModal] = useState(false)
  const showClaimPopup = useShowClaimPopup()
  const dissmissChangeNetwork = () => setChangeNetworkIsOpen(false)
  const countUpValue = aggregateBalance?.toFixed(0) ?? '0'
  const countUpValuePrevious = usePrevious(countUpValue) ?? '0'

  const scrollY = useScrollPosition()
  const greenDot = () => {
    return (
      <GreenDot>
        <div></div>
      </GreenDot>
    )
  }
  const changeChainId = async (chainId: string, rpcUrl: string, chainName: string) => {
    try {
      await library?.send('wallet_switchEthereumChain', [{ chainId: chainId }, account])
    } catch (switchError) {
      // This error code indicates that the chain has not been added to MetaMask.
      if (switchError.code === 4902) {
        try {
          console.log('ADD MATIC')
          await library?.send('wallet_addEthereumChain', [
            { chainId: chainId, rpcUrls: [rpcUrl], chainName: 'Matic' },
            account
          ])
        } catch (addError) {
          // handle "add" error
        }
      }
      // handle other "switch" errors
    }
  }
  const updateNetwork = async (network: any) => {
    console.log(network)
    if (!account) {
      if (network == 1) {
        if (isMainnet) {
          localStorage.setItem('chainId', '56')
          if (chainId !== 56) {
            window.location.reload()
          }
        } else {
          localStorage.setItem('chainId', '4')
          if (chainId !== 4) {
            window.location.reload()
          }
        }
      } else {
        if (isMainnet) {
          localStorage.setItem('chainId', '137')
          if (chainId !== 137) {
            window.location.reload()
          }
        } else {
          localStorage.setItem('chainId', '80001')
          if (chainId !== 80001) {
            window.location.reload()
          }
        }
      }
      return
    }
    if (network == 1) {
      if (isMainnet) {
        await changeChainId('0x38', 'https://nodes.pancakeswap.com/', 'Binance Smart Chain')
      } else {
        await changeChainId('0x4', 'https://rinkeby.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161', 'Rinkeby')
      }
    } else {
      if (isMainnet) {
        await changeChainId('0x89', 'https://polygon-rpc.com', 'Polygon')
      } else {
        await changeChainId('0x13881', 'https://matic-mumbai.chainstacklabs.com', 'Mumbai')
      }
    }
    dissmissChangeNetwork()
  }
  return (
    <HeaderFrame showBackground={scrollY > 45}>
      <ClaimModal />
      <Modal isOpen={showUniBalanceModal} onDismiss={() => setShowUniBalanceModal(false)}>
        <UniBalanceContent setShowUniBalanceModal={setShowUniBalanceModal} />
      </Modal>
      <Modal isOpen={changeNetworkIsOpen} onDismiss={dissmissChangeNetwork} maxHeight={98}>
        <div
          style={{
            marginLeft: '2rem',
            marginRight: 'auto',
            width: '100%'
          }}
        >
          <HeaderRow style={{ fontSize: '18px', marginRight: '2.5rem', marginTop: '1rem' }}>
            <CloseIcon onClick={dissmissChangeNetwork}>
              <CloseColor />
            </CloseIcon>
          </HeaderRow>
          <h3>Switch network</h3>
          <ConnectSelect
            className="connect-select"
            onClick={() => {
              updateNetwork(1)
            }}
          >
            {[56].includes(Number(chainId)) && greenDot()}
            <span className="wallet-text" style={{ alignSelf: 'center' }}>
              Binance Smart Chain
            </span>
            <div style={{ marginLeft: 'auto' }}>
              <img src={"https://s2.coinmarketcap.com/static/img/coins/64x64/7192.png"} width={30} height={30} />
            </div>
          </ConnectSelect>
          <ConnectSelect
            className="connect-select"
            onClick={() => {
              updateNetwork(2)
            }}
          >
            {[80001, 137].includes(Number(chainId)) && greenDot()}
            <span className="wallet-text" style={{ alignSelf: 'center' }}>
              Polygon
            </span>
            <div style={{ marginLeft: 'auto' }}>
              <img src={PolygonImage} width={30} height={30} />
            </div>
          </ConnectSelect>
        </div>
      </Modal>
      <HeaderRow>
        <Title href=".">
          <TomiIcon>
            <img src={darkMode ? LogoDark : Logo} alt="logo" />
          </TomiIcon>
        </Title>
      </HeaderRow>
      <HeaderLinks>
        <StyledNavLink id={`swap-nav-link`} to={'/swap'}>
          Swap
        </StyledNavLink>
        <StyledNavLink
          id={`pool-nav-link`}
          to={'/pool'}
          isActive={(match, { pathname }) =>
            Boolean(match) ||
            pathname.startsWith('/add') ||
            pathname.startsWith('/remove') ||
            pathname.startsWith('/create') ||
            pathname.startsWith('/find')
          }
        >
          Pool
        </StyledNavLink>
        {/* <StyledNavLink id={`stake-nav-link`} to={'/uni'}>
          UNI
        </StyledNavLink> */}
        {/* <StyledNavLink
          id={`vote-nav-link`}
          to={'/vote'}
          isActive={(match, { pathname }) => Boolean(match) || pathname.startsWith('/proposal-create')}
        >
          Vote
        </StyledNavLink>
        <StyledNavLink
          id={`vote-nav-link`}
          to={'/stake'}
          isActive={(match, { pathname }) => Boolean(match) || pathname.startsWith('/stake')}
        >
          Staking
        </StyledNavLink>
        <StyledExternalLink id={`stake-nav-link`} href={'https://info.tomiswap.org'}>
          Charts <span style={{ fontSize: '11px' }}>â†—</span>
        </StyledExternalLink> */}
      </HeaderLinks>
      <HeaderControls>
        <HeaderElement>
          <HideSmall>
            {chainId && NETWORK_LABELS[chainId] && (
              <NetworkCard
                title={NETWORK_LABELS[chainId]}
                onClick={() => {
                  setChangeNetworkIsOpen(true)
                }}
              >
                {chainId === 1 ? 'Binance' : NETWORK_LABELS[chainId]}
              </NetworkCard>
            )}
          </HideSmall>
          <AccountElement active={!!account} style={{ pointerEvents: 'auto' }}>
            {account && userEthBalance && !isMobile ? (
              <BalanceText>
                {convertToNumberDecimals(userEthBalance, userEthBalance.currency.decimals)} {coinName}
              </BalanceText>
            ) : null}
            <Web3Status />
          </AccountElement>
        </HeaderElement>
      </HeaderControls>
    </HeaderFrame>
  )
}
