import { TransactionErrorContent } from 'components/TransactionConfirmationModal'
import React, { useState } from 'react'

import Modal from '../Modal'

interface ErrorModalProps {
  isOpen: boolean
  onDismiss: () => void
  message?: string
}

export default function RejectModal({ isOpen, onDismiss, message }: ErrorModalProps) {
  return (
    <Modal isOpen={isOpen} onDismiss={onDismiss} maxHeight={90}>
      <TransactionErrorContent message={message ? message : 'Transaction rejected'} onDismiss={onDismiss} />
    </Modal>
  )
}
