import { Link, useHistory } from 'react-router-dom'
import { ButtonPrimary } from '../../components/Button'
import { CardNoise } from '../earn/styled'
import Card, { LightCard } from '../Card'
import { AutoColumn } from '../Column'
import Row, { RowBetween, RowFixed, AutoRow } from '../Row'
import { truncate } from 'utils/bigNumber-util'
import { useTokenContract } from 'hooks/useContract'

import CurrencyLogo from '../../components/CurrencyLogo'

import { useCurrency, useToken } from '../../hooks/Tokens'
import { useMintRewardCallback, useTomiReward } from '../../hooks/useLpReward'
import { Dots } from '../../pages/Pool/styleds'
import { unwrappedToken } from '../../utils/wrappedCurrency'
import { WETH, ChainId } from 'tomiswap-sdk'
import { TomiStorage } from 'model/storage'
import { isMobile } from 'react-device-detect'

export const FixedHeightRow = styled(RowBetween)`
  height: 24px;
`

export const HoverCard = styled(Card)`
  border: 1px solid transparent;
  :hover {
    border: 1px solid ${({ theme }) => darken(0.06, theme.bg2)};
  }
`

const PositionCardWrapper = styled.div``

const StyledPositionCard = styled(LightCard)`
  border: none;
  background: #232428;
  position: relative;
  overflow: hidden;
  border: 1px solid ${({ theme }) => theme.bg3};
  border-radius: 8px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
  padding: 1.25rem 0.5rem; 
`};
`
const ButtonListLiquidity = styled(ButtonPrimary)`
  width: 48.5%;
  padding: 18px 0;
  border-radius: 8px;
  color: ${({ theme }) => theme.text6};
  font-weight: 600;
`
const ButtonClaim = styled(ButtonPrimary)`
  width: fit-content;
  padding: 5px;
  margin-left: 5px;
  border-radius: 8px;
  color: ${({ theme }) => theme.text6};
  font-size: 1rem;
  font-weight: 600;
`
const TextListLiquidity = styled.div`
  font-size: 14px;
  font-weight: 600;
  ${({ theme }) => theme.mediaWidth.upToMedium`
  font-size: 14px;
`};
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
font-size: 12px;
`};
`
const LiquidityIconWrapper = styled.div`
  z-index: 1;
  cursor: pointer;
  margin-left: 10px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
`
interface PositionCardProps {
  pair: any
  idx: number
  checkAmountCard: () => void
  showDetail?: boolean
  handleClickCard: (idx: number) => void
  chainId?: ChainId
}

export function LiquiPositionCard({
  pair,
  checkAmountCard,
  handleClickCard,
  showDetail,
  idx,
  chainId = 1
}: PositionCardProps) {
  //  const [showMore, setShowMore] = useState(false)
  const history = useHistory()

  const token0 = useTokenContract(pair['token0'])
  const token1 = useTokenContract(pair['token1'])

  const [index, setIndex] = useState(0)

  const reward = useTomiReward(pair['address'], index)

  const poolShare = Number(pair['percent'] * 100).toFixed(2)

  const [loadingClaim, mintReward] = useMintRewardCallback(pair['address'], () => setIndex(index + 1))

  const tokenA = useToken(pair['token0'])

  const tokenB = useToken(pair['token1'])

  const decimalA = (tokenA?.decimals || 2) >= 8 ? 8 : tokenA?.decimals
  const decimalB = (tokenB?.decimals || 2) >= 8 ? 8 : tokenB?.decimals
  const unWrapTokenA = tokenA ? unwrappedToken(tokenA) : undefined
  const unWrapTokenB = tokenB ? unwrappedToken(tokenB) : undefined

  function onRemoveBtnClickHandler() {
    history.push(redirectRouterRemoveLiquidity)
  }

  useEffect(() => {
    if (!(Number(poolShare) === 0 && (reward === '' || Number(reward) === 0))) {
      checkAmountCard()
    }
  }, [poolShare, reward])
  // console.log('XXX', token0?.address, WETH[chainId].address)
  const oneTokenIsWETH = token0?.address === WETH[chainId].address || token1?.address === WETH[chainId].address

  const redirectRouterAddLiquidity = oneTokenIsWETH
    ? `/add/ETH/${token0?.address === WETH[chainId].address ? token1?.address : token0?.address}`
    : `/add/${token0?.address}/${token1?.address}`

  const redirectRouterRemoveLiquidity = oneTokenIsWETH
    ? `/remove/${token0?.address === WETH[chainId].address ? 'ETH/' + token1?.address : token0?.address + '/ETH'}`
    : `/remove/${token0?.address}/${token1?.address}`

  return (
    <>
      {Number(poolShare) === 0 && (reward === '' || Number(reward) === 0) ? (
        <></>
      ) : (
        <PositionCardWrapper>
          <StyledPositionCard onClick={() => handleClickCard(showDetail ? -1 : idx)}>
            <CardNoise />
            <AutoColumn gap="12px">
              <FixedHeightRow>
                <AutoRow gap="5px" style={{ width: 'fit-content' }}>
                  <CurrencyLogo
                    currency={unWrapTokenA}
                    size={isMobile ? '15px' : '20px'}
                    style={{ marginRight: '5px' }}
                  />
                  <TextListLiquidity>{unWrapTokenA?.symbol}</TextListLiquidity>
                  <ChevronRight size="20" style={{ color: '#e01286' }} />
                  <CurrencyLogo currency={unWrapTokenB} size={'20px'} style={{ marginRight: '12px' }} />
                  <TextListLiquidity>{unWrapTokenB?.symbol}</TextListLiquidity>
                </AutoRow>
                <RowFixed gap="8px">
                  {showDetail ? (
                    <>
                      <TextListLiquidity>TOMI : {truncate(Number(reward), 8)}</TextListLiquidity>
                      <LiquidityIconWrapper>
                        <ChevronUp size="30"/>
                      </LiquidityIconWrapper>
                    </>
                  ) : (
                    <>
                      <TextListLiquidity>TOMI : {truncate(Number(reward), 8)}</TextListLiquidity>
                      <LiquidityIconWrapper>
                        <ChevronDown size="30"/>
                      </LiquidityIconWrapper>
                    </>
                  )}
                </RowFixed>
              </FixedHeightRow>
            </AutoColumn>
          </StyledPositionCard>
          {showDetail && (
            <StyledPositionCard style={{ margin: '2rem 0' }}>
              <AutoColumn gap="12px">
                <FixedHeightRow>
                  <TextListLiquidity>Pooled {unWrapTokenA?.symbol}</TextListLiquidity>
                  <TextListLiquidity>
                    {truncate(Number(pair['reserve0']) * pair['percent'], decimalA)}
                    {/* {roundToTwo((Number(pair['reserve0']) * pair['percent']),8) } */}
                  </TextListLiquidity>
                </FixedHeightRow>
                <FixedHeightRow>
                  <TextListLiquidity>Pooled {unWrapTokenB?.symbol}</TextListLiquidity>
                  <TextListLiquidity>
                    {truncate(Number(pair['reserve1']) * pair['percent'], decimalB)}
                  </TextListLiquidity>
                </FixedHeightRow>
                <FixedHeightRow>
                  <TextListLiquidity style={{ minWidth: 'fit-content' }}>Total Reward</TextListLiquidity>
                  {Number(reward) === 0 ? (
                    <Row style={{ justifyContent: 'flex-end', alignItems: 'center' }}>
                      <TextListLiquidity>{truncate(Number(reward), 8)}</TextListLiquidity>
                    </Row>
                  ) : (
                    <Row style={{ justifyContent: 'flex-end', alignItems: 'center' }}>
                      <TextListLiquidity>{truncate(Number(reward), 8)}</TextListLiquidity>
                      <ButtonClaim onClick={() => mintReward()}>
                        {loadingClaim ? <Dots>Claiming</Dots> : <TextListLiquidity>Claim</TextListLiquidity>}
                      </ButtonClaim>
                    </Row>
                  )}
                </FixedHeightRow>
                <FixedHeightRow>
                  <TextListLiquidity>LP numbers</TextListLiquidity>
                  <TextListLiquidity>{truncate(Number(pair['liquidityShow']), 8)}</TextListLiquidity>
                </FixedHeightRow>
                <FixedHeightRow>
                  <TextListLiquidity>Your pool share</TextListLiquidity>
                  <TextListLiquidity>{truncate(pair['percent'] * 100, 2)} %</TextListLiquidity>
                </FixedHeightRow>
                <RowBetween style={{ margin: '3rem 0 1rem' }}>
                  <ButtonListLiquidity
                    id="join-pool-button"
                    as={Link}
                    padding="6px 8px"
                    to={redirectRouterAddLiquidity}
                  >
                    <TextListLiquidity>Add</TextListLiquidity>
                  </ButtonListLiquidity>
                  <ButtonListLiquidity
                    id="join-pool-button"
                    padding="6px 8px"
                    disabled={Number(pair['liquidityShow']) === 0 ? true : false}
                    onClick={onRemoveBtnClickHandler}
                  >
                    <TextListLiquidity>Remove</TextListLiquidity>
                  </ButtonListLiquidity>
                </RowBetween>
              </AutoColumn>
            </StyledPositionCard>
          )}
        </PositionCardWrapper>
      )}
    </>
  )
}
