import { TransactionResponse } from '@ethersproject/providers'
import { currencyEquals, ETHER, MATIC, Percent, WETH } from 'tomiswap-sdk'

import React, { useCallback, useContext, useMemo, useState, useEffect } from 'react'
import { Plus } from 'react-feather'
import ReactGA from 'react-ga'
import { RouteComponentProps } from 'react-router'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { ButtonPrimary, ButtonLight, ButtonError } from '../../components/Button'
import { BlueCard, LightCard } from '../../components/Card'
import { AutoColumn } from '../../components/Column'
import TransactionConfirmationModal, { ConfirmationModalContent } from '../../components/TransactionConfirmationModal'
import DoubleCurrencyLogo from '../../components/DoubleLogo'
import { AddRemoveTabs } from '../../components/NavigationTabs'
import { MinimalPositionCard } from '../../components/PositionCard'
import { RowBetween, RowFixed, RowFixedResponsive } from '../../components/Row'
import Slider from '../../components/Slider'
import CurrencyLogo from '../../components/CurrencyLogo'
import { useActiveWeb3React } from '../../hooks'
import { useCurrency } from '../../hooks/Tokens'
import useTransactionDeadline from '../../hooks/useTransactionDeadline'

import { useTransactionAdder } from '../../state/transactions/hooks'
import { StyledInternalLink, TYPE } from '../../theme'
import { calculateGasMargin, calculateSlippageAmount, getRouterContract } from '../../utils'
import useDebouncedChangeHandler from '../../utils/useDebouncedChangeHandler'
import { wrappedCurrency } from '../../utils/wrappedCurrency'
import { BodyWrapper } from '../AppBody'
import { MaxButton, Wrapper } from '../Pool/styleds'
import { useBurnActionHandlers } from '../../state/burn/hooks'
import { useDerivedBurnInfo, useBurnState } from '../../state/burn/hooks'
import { Field } from '../../state/burn/actions'
import { useWalletModalToggle } from '../../state/application/hooks'
import { useUserSlippageTolerance } from '../../state/user/hooks'
import { BigNumber } from '@ethersproject/bignumber'
import { useHasTransactionSuccess } from '../../state/transactions/hooks'
import { useAddPopup } from '../../state/application/hooks'
import { isMobile } from 'react-device-detect'
import { size } from 'lodash'
import { convertToNumberDecimals } from 'utils'
import { truncate } from 'utils/bigNumber-util'

export default function RemoveLiquidity({
  match: {
    params: { currencyIdA, currencyIdB }
  },
  history
}: RouteComponentProps<{ currencyIdA: string; currencyIdB: string }>) {
  const [currencyA, currencyB] = [useCurrency(currencyIdA) ?? undefined, useCurrency(currencyIdB) ?? undefined]
  const { account, chainId, library } = useActiveWeb3React()
  const [tokenA, tokenB] = useMemo(() => [wrappedCurrency(currencyA, chainId), wrappedCurrency(currencyB, chainId)], [
    currencyA,
    currencyB,
    chainId
  ])

  const theme = useContext(ThemeContext)

  // toggle wallet when disconnected
  const toggleWalletModal = useWalletModalToggle()

  // burn state
  const { independentField, typedValue } = useBurnState()
  const { pair, parsedAmounts, error } = useDerivedBurnInfo(currencyA ?? undefined, currencyB ?? undefined)
  const { onUserInput: _onUserInput } = useBurnActionHandlers()
  const isValid = !error
  const [isOnRemove, setIsOnRemove] = useState(false)

  // modal and loading
  const [showConfirm, setShowConfirm] = useState<boolean>(false)
  const [attemptingTxn, setAttemptingTxn] = useState(false) // clicked confirm

  // txn values
  const [txHash, setTxHash] = useState<string>('')
  const deadline = useTransactionDeadline()
  const [allowedSlippage] = useUserSlippageTolerance()
  // wrapped onUserInput to clear signatures
  const onUserInput = useCallback(
    (field: Field, typedValue: string) => {
      return _onUserInput(field, typedValue)
    },
    [_onUserInput]
  )
  useEffect(() => {
    onUserInput(Field.LIQUIDITY_PERCENT, '0')
  }, [])
  // Back to pool page when remove done
  const success = useHasTransactionSuccess(txHash)
  useEffect(() => {
    if (success) {
      setIsOnRemove(false)
      history.push('/pool')
      setTxHash('')
    }
  }, [success, setIsOnRemove, history, setTxHash])

  const liquidityToken = parsedAmounts[Field.LIQUIDITY]?.token

  const convertCurrencyAmount = (currencyAcmount?: any, decimals?: number) => {
    return (
      convertToNumberDecimals(
        Number(currencyAcmount?.raw || 0) / Math.pow(10, decimals !== undefined ? decimals : 18),
        decimals
      ) ?? ''
    )
  }

  const formattedAmounts = {
    [Field.LIQUIDITY_PERCENT]: parsedAmounts[Field.LIQUIDITY_PERCENT].equalTo('0')
      ? '0'
      : parsedAmounts[Field.LIQUIDITY_PERCENT].lessThan(new Percent('1', '100'))
      ? '<1'
      : parsedAmounts[Field.LIQUIDITY_PERCENT].toFixed(0),
    [Field.LIQUIDITY]:
      independentField === Field.LIQUIDITY
        ? typedValue
        : convertCurrencyAmount(parsedAmounts[Field.LIQUIDITY], liquidityToken?.decimals),
    [Field.CURRENCY_A]:
      independentField === Field.CURRENCY_A
        ? typedValue
        : convertCurrencyAmount(parsedAmounts[Field.CURRENCY_A], tokenA?.decimals),
    [Field.CURRENCY_B]:
      independentField === Field.CURRENCY_B
        ? typedValue
        : convertCurrencyAmount(parsedAmounts[Field.CURRENCY_B], tokenB?.decimals)
  }

  // show popup when error
  const addPopup = useAddPopup()

  // tx sending
  const addTransaction = useTransactionAdder()
  async function onRemove() {
    if (!chainId || !library || !account || !deadline) throw new Error('missing dependencies')
    const { [Field.CURRENCY_A]: currencyAmountA, [Field.CURRENCY_B]: currencyAmountB } = parsedAmounts
    if (!currencyAmountA || !currencyAmountB) {
      throw new Error('missing currency amounts')
    }
    const router = getRouterContract(chainId, library, account)

    const amountsMin = {
      [Field.CURRENCY_A]: calculateSlippageAmount(currencyAmountA, allowedSlippage)[0],
      [Field.CURRENCY_B]: calculateSlippageAmount(currencyAmountB, allowedSlippage)[0]
    }

    if (!currencyA || !currencyB) throw new Error('missing tokens')
    const liquidityAmount = parsedAmounts[Field.LIQUIDITY]

    if (!liquidityAmount) throw new Error('missing liquidity amount')

    const currencyBIsETH = currencyB === ETHER || currencyB === MATIC
    const oneCurrencyIsETH = currencyA === ETHER || currencyA === MATIC || currencyBIsETH

    if (!tokenA || !tokenB) throw new Error('could not wrap')

    let methodNames: string[], args: Array<string | string[] | number | boolean>
    // removeLiquidityETH
    if (oneCurrencyIsETH) {
      methodNames = ['removeLiquidityETH']
      args = [
        currencyBIsETH ? tokenA.address : tokenB.address,
        liquidityAmount.raw.toString(),
        amountsMin[currencyBIsETH ? Field.CURRENCY_A : Field.CURRENCY_B].toString(),
        amountsMin[currencyBIsETH ? Field.CURRENCY_B : Field.CURRENCY_A].toString(),
        deadline.toHexString()
      ]
    }
    // removeLiquidity
    else {
      methodNames = ['removeLiquidity']
      args = [
        tokenA.address,
        tokenB.address,
        liquidityAmount.raw.toString(),
        amountsMin[Field.CURRENCY_A].toString(),
        amountsMin[Field.CURRENCY_B].toString(),
        deadline.toHexString()
      ]
    }

    const safeGasEstimates: (BigNumber | undefined)[] = await Promise.all(
      methodNames.map(methodName =>
        router.estimateGas[methodName](...args)
          .then(calculateGasMargin)
          .catch(error => {
            addPopup({
              error: {
                summary: 'Remove ' + currencyA?.symbol + '/' + currencyB?.symbol + ' liquidity failed'
              }
            })
            console.error(`estimateGas failed`, methodName, args, error)
            history.push('/pool')
            return undefined
          })
      )
    )

    const indexOfSuccessfulEstimation = safeGasEstimates.findIndex(safeGasEstimate =>
      BigNumber.isBigNumber(safeGasEstimate)
    )

    // all estimations failed...
    if (indexOfSuccessfulEstimation === -1) {
      console.error('This transaction would fail. Please contact support.')
    } else {
      const methodName = methodNames[indexOfSuccessfulEstimation]
      const safeGasEstimate = safeGasEstimates[indexOfSuccessfulEstimation]

      setAttemptingTxn(true)
      await router[methodName](...args, {
        gasLimit: safeGasEstimate
      })
        .then((response: TransactionResponse) => {
          setAttemptingTxn(false)

          addTransaction(response, {
            summary: 'Remove ' + currencyA?.symbol + '/' + currencyB?.symbol + ' liquidity'
          })
          setIsOnRemove(true)
          setTxHash(response.hash)

          ReactGA.event({
            category: 'Liquidity',
            action: 'Remove',
            label: [currencyA?.symbol, currencyB?.symbol].join('/')
          })
        })
        .catch((error: Error) => {
          setAttemptingTxn(false)
          // we only care if the error is something _other_ than the user rejected the tx
          console.error(error)
          addPopup({
            error: {
              summary: 'Remove ' + currencyA?.symbol + '/' + currencyB?.symbol + ' liquidity failed'
            }
          })
          history.push('/pool')
        })
    }
  }

  function modalHeader() {
    return (
      <AutoColumn gap={'md'} style={{ marginTop: '20px' }}>
        <RowBetween align="flex-end">
          <Text fontSize={24} fontWeight={600}>
            {parsedAmounts[Field.CURRENCY_A]?.toFixed((tokenA?.decimals || 2) >= 6 ? 6 : tokenA?.decimals)}
          </Text>
          <RowFixed gap="4px">
            <CurrencyLogo currency={currencyA} size={isMobile ? '35px' : '24px'} />
            <Text fontSize={24} fontWeight={600} style={{ marginLeft: '10px' }}>
              {currencyA?.symbol}
            </Text>
          </RowFixed>
        </RowBetween>
        <RowFixed>
          <Plus size="16" color={theme.text2} fontWeight={600} />
        </RowFixed>
        <RowBetween align="flex-end">
          <Text fontSize={24} fontWeight={600}>
            {parsedAmounts[Field.CURRENCY_B]?.toFixed((tokenB?.decimals || 2) >= 6 ? 6 : tokenB?.decimals)}
          </Text>
          <RowFixed gap="4px">
            <CurrencyLogo currency={currencyB} size={isMobile ? '35px' : '24px'} />
            <Text fontSize={24} fontWeight={600} style={{ marginLeft: '10px' }}>
              {currencyB?.symbol}
            </Text>
          </RowFixed>
        </RowBetween>

        <TYPE.small fontSize={14} color={theme.primary1} textAlign="left" padding={'12px 0 0 0'}>
          {`Output is estimated. If the price changes by more than ${allowedSlippage /
            100}% your transaction will revert.`}
        </TYPE.small>
      </AutoColumn>
    )
  }

  function modalBottom() {
    return (
      <>
        <RowBetween>
          <Text color={theme.white} fontWeight={600} fontSize={16}>
            {'TOMI ' + currencyA?.symbol + '/' + currencyB?.symbol} Burned
          </Text>
          <RowFixed>
            <DoubleCurrencyLogo currency0={currencyA} currency1={currencyB} margin={true} />
            <Text fontWeight={600} fontSize={16}>
              {truncate(Number(parsedAmounts[Field.LIQUIDITY]?.raw || 0) / Math.pow(10, 18), liquidityToken?.decimals)}
            </Text>
          </RowFixed>
        </RowBetween>
        {pair && (
          <>
            <RowBetween>
              <Text color={theme.white} fontWeight={600} fontSize={16}>
                Price
              </Text>
              <Text fontWeight={600} fontSize={16} color={theme.text1}>
                1 {currencyA?.symbol} ={' '}
                {tokenA && tokenB ? convertToNumberDecimals(pair.priceOf(tokenA), tokenB?.decimals) : '-'}{' '}
                {/* {tokenA && tokenB ? pair.priceOf(tokenA).toFixed(tokenB?.decimals >= 8 ? 8 : tokenB?.decimals) : '-'}{' '} */}
                {currencyB?.symbol}
              </Text>
            </RowBetween>
            <RowBetween>
              <div />
              <Text fontWeight={600} fontSize={16} color={theme.text1}>
                1 {currencyB?.symbol} ={' '}
                {tokenA && tokenB ? convertToNumberDecimals(pair.priceOf(tokenB), tokenA?.decimals) : '-'}{' '}
                {/* {tokenA && tokenB ? pair.priceOf(tokenB).toFixed(tokenA?.decimals >= 8 ? 8 : tokenA?.decimals) : '-'}{' '} */}
                {currencyA?.symbol}
              </Text>
            </RowBetween>
          </>
        )}
        <ButtonPrimary onClick={onRemove}>
          <Text fontWeight={700} fontSize={16}>
            Confirm
          </Text>
        </ButtonPrimary>
      </>
    )
  }

  const pendingText = `Removing ${parsedAmounts[Field.CURRENCY_A]?.toFixed(
    (tokenA?.decimals || 2) >= 6 ? 6 : tokenA?.decimals
  )} ${currencyA?.symbol} and ${parsedAmounts[Field.CURRENCY_B]?.toFixed(
    (tokenB?.decimals || 2) >= 6 ? 6 : tokenB?.decimals
  )} ${currencyB?.symbol}`

  const liquidityPercentChangeCallback = useCallback(
    (value: number) => {
      onUserInput(Field.LIQUIDITY_PERCENT, value.toString())
    },
    [onUserInput]
  )

  const oneCurrencyIsETH = currencyA === ETHER || currencyB === ETHER
  const oneCurrencyIsWETH = Boolean(
    chainId &&
      ((currencyA && currencyEquals(WETH[chainId], currencyA)) ||
        (currencyB && currencyEquals(WETH[chainId], currencyB)))
  )

  const handleDismissConfirmation = useCallback(() => {
    setShowConfirm(false)
    // if there was a tx hash, we want to clear the input
    if (txHash) {
      onUserInput(Field.LIQUIDITY_PERCENT, '0')
    }
    // setTxHash('')
  }, [onUserInput, txHash])

  const [innerLiquidityPercentage, setInnerLiquidityPercentage] = useDebouncedChangeHandler(
    Number.parseInt(parsedAmounts[Field.LIQUIDITY_PERCENT].toFixed(0)),
    liquidityPercentChangeCallback
  )

  return (
    <>
      <BodyWrapper style={{ maxWidth: 640, border: '1px solid #e01286' }}>
        <AddRemoveTabs creating={false} adding={false} />
        <Wrapper>
          <TransactionConfirmationModal
            isOpen={showConfirm}
            onDismiss={handleDismissConfirmation}
            attemptingTxn={attemptingTxn}
            hash={txHash ? txHash : ''}
            content={() => (
              <ConfirmationModalContent
                title={'You will receive'}
                onDismiss={handleDismissConfirmation}
                topContent={modalHeader}
                bottomContent={modalBottom}
              />
            )}
            pendingText={pendingText}
          />
          <AutoColumn gap="md">
            {/* <BlueCard>
              <AutoColumn gap="10px">
                <TYPE.link fontWeight={400} color={'primaryText1'}>
                  <b>Tip:</b> Removing pool tokens converts your position back into underlying tokens at the current
                  rate, proportional to your share of the pool. Accrued fees are included in the amounts you receive.
                </TYPE.link>
              </AutoColumn>
            </BlueCard> */}
            <AutoColumn gap="20px">
              <Slider value={innerLiquidityPercentage} size={25} onChange={setInnerLiquidityPercentage} />
              <RowBetween>
                <MaxButton
                  style={{ fontSize: '14px' }}
                  onClick={() => onUserInput(Field.LIQUIDITY_PERCENT, '0')}
                  width="20%"
                >
                  0%
                </MaxButton>
                <MaxButton
                  style={{ fontSize: '14px' }}
                  onClick={() => onUserInput(Field.LIQUIDITY_PERCENT, '25')}
                  width="20%"
                >
                  25%
                </MaxButton>
                <MaxButton
                  style={{ fontSize: '14px' }}
                  onClick={() => onUserInput(Field.LIQUIDITY_PERCENT, '50')}
                  width="20%"
                >
                  50%
                </MaxButton>
                <MaxButton
                  style={{ fontSize: '14px' }}
                  onClick={() => onUserInput(Field.LIQUIDITY_PERCENT, '75')}
                  width="20%"
                >
                  75%
                </MaxButton>
                <MaxButton
                  style={{ fontSize: '14px' }}
                  onClick={() => onUserInput(Field.LIQUIDITY_PERCENT, '100')}
                  width="20%"
                >
                  100%
                </MaxButton>
              </RowBetween>
            </AutoColumn>
            <LightCard>
              <AutoColumn gap="10px">
                <RowBetween>
                  <AutoColumn>
                    <RowBetween>
                      <AutoColumn>
                        <CurrencyLogo
                          currency={currencyA}
                          size={isMobile ? '35px' : '50px'}
                          style={{ marginRight: '12px' }}
                        />
                      </AutoColumn>
                      <AutoColumn>
                        <RowFixedResponsive>
                          <Text fontSize={16} fontWeight={600} id="remove-liquidity-tokena-symbol">
                            {currencyA?.symbol}
                          </Text>
                        </RowFixedResponsive>
                        <RowFixedResponsive>
                          <Text fontSize={16} fontWeight={600}>
                            {formattedAmounts[Field.CURRENCY_A] ||
                              (tokenA && tokenA?.decimals >= 8
                                ? '0.00000000'
                                : tokenA && tokenA?.decimals == 0
                                ? 0
                                : '0.' + '0'.repeat(tokenA?.decimals ? tokenA?.decimals : 0))}
                          </Text>
                        </RowFixedResponsive>
                      </AutoColumn>
                    </RowBetween>
                  </AutoColumn>
                  <AutoColumn>
                    <RowBetween>
                      <AutoColumn>
                        <CurrencyLogo
                          currency={currencyB}
                          size={isMobile ? '35px' : '50px'}
                          style={{ marginRight: '12px' }}
                        />
                      </AutoColumn>
                      <AutoColumn>
                        <RowFixedResponsive>
                          <Text fontSize={16} fontWeight={600} id="remove-liquidity-tokenb-symbol">
                            {currencyB?.symbol}
                          </Text>
                        </RowFixedResponsive>
                        <RowFixedResponsive>
                          <Text fontSize={16} fontWeight={600}>
                            {formattedAmounts[Field.CURRENCY_B] ||
                              (tokenB && tokenB?.decimals >= 6
                                ? '0.00000000'
                                : tokenB && tokenB?.decimals == 6
                                ? 0
                                : '0.' + '0'.repeat(tokenB?.decimals ? tokenB?.decimals : 0))}
                          </Text>
                        </RowFixedResponsive>
                      </AutoColumn>
                    </RowBetween>
                  </AutoColumn>
                </RowBetween>
                {/* {chainId && (oneCurrencyIsWETH || oneCurrencyIsETH) ? (
                  <RowBetween style={{ justifyContent: 'flex-end' }}>
                    {oneCurrencyIsETH ? (
                      <StyledInternalLink
                        to={`/remove/${currencyA === ETHER ? WETH[chainId].address : currencyIdA}/${currencyB === ETHER ? WETH[chainId].address : currencyIdB
                          }`}
                      >
                        Receive WETH
                      </StyledInternalLink>
                    ) : oneCurrencyIsWETH ? (
                      <StyledInternalLink
                        to={`/remove/${currencyA && currencyEquals(currencyA, WETH[chainId]) ? 'ETH' : currencyIdA}/${currencyB && currencyEquals(currencyB, WETH[chainId]) ? 'ETH' : currencyIdB
                          }`}
                      >
                        Receive ETH
                      </StyledInternalLink>
                    ) : null}
                  </RowBetween>
                ) : null} */}
                {pair && (
                  <div style={{ paddingTop: '20px' }}>
                    <RowBetween>
                      <AutoColumn>
                        <RowFixedResponsive>
                          <Text fontSize={16} fontWeight={600} color={'#e01286'} style={{ marginRight: '22px' }}>
                            Price
                          </Text>
                          <Text fontSize={14} fontWeight={400}>
                            1 {currencyA?.symbol} ={' '}
                            {tokenA && tokenB ? convertToNumberDecimals(pair.priceOf(tokenA), tokenB?.decimals) : '-'}{' '}
                            {currencyB?.symbol}
                          </Text>
                        </RowFixedResponsive>
                      </AutoColumn>
                      <AutoColumn>
                        <RowFixedResponsive>
                          <Text fontSize={14} fontWeight={400}>
                            1 {currencyB?.symbol} ={' '}
                            {tokenA && tokenB ? convertToNumberDecimals(pair.priceOf(tokenB), tokenA?.decimals) : '-'}{' '}
                            {currencyA?.symbol}
                          </Text>
                        </RowFixedResponsive>
                      </AutoColumn>
                    </RowBetween>
                  </div>
                )}
              </AutoColumn>
            </LightCard>
            <div style={{ position: 'relative' }}>
              {!account ? (
                <ButtonLight onClick={toggleWalletModal}>Connect to a wallet</ButtonLight>
              ) : (
                <RowBetween>
                  <ButtonError
                    onClick={() => {
                      setShowConfirm(true)
                    }}
                    disabled={!isValid || isOnRemove}
                    error={!isValid && !!parsedAmounts[Field.CURRENCY_A] && !!parsedAmounts[Field.CURRENCY_B]}
                  >
                    <Text fontSize={16} fontWeight={600}>
                      {error || 'Remove'}
                    </Text>
                  </ButtonError>
                </RowBetween>
              )}
            </div>
          </AutoColumn>
        </Wrapper>
      </BodyWrapper>

      {pair ? (
        <AutoColumn style={{ minWidth: '20rem', width: '100%', maxWidth: '400px', marginTop: '1rem' }}>
          <MinimalPositionCard showUnwrapped={oneCurrencyIsWETH} pair={pair} />
        </AutoColumn>
      ) : null}
    </>
  )
}
