import Web3 from 'web3'
import TOMICONFIG from '../constants/abis/tomi-config.json'
import { TOMI_CONFIG } from '../constants/addresses'

const FULL_NODE = process.env.REACT_APP_NETWORK_URL

async function loadWeb3() {
  if (window.ethereum && window.ethereum.isMetaMask) {
    window.web3 = new Web3(window.ethereum)
    // window.ethereum.enable()
  } else {
    window.web3 = new Web3(FULL_NODE)
  }
}

async function load() {
  await loadWeb3()
}

// Use web3.utils.asciiToHex before you send the string to the smart contract
// Use web3.utils.hexToAscii after you read the string from the smart contract

const connectToTomiConfigSC = async (value, chainId) => {
  await load()
  const convertToByte32 = Web3.utils.asciiToHex(value)
  try {
    
    const contract = new window.web3.eth.Contract(TOMICONFIG, chainId !== undefined ? TOMI_CONFIG[chainId] : undefined)
    console.log('contract', contract)
    const result = await contract.methods.getConfig(convertToByte32).call()
    return result
  } catch (e) {
    console.log('error ---->', e)
  }
}

export default connectToTomiConfigSC
