import React, { useCallback, useContext, useEffect, useState, useMemo } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { Link } from 'react-router-dom'
import { SwapPoolTabs } from '../../components/NavigationTabs'

import { LiquiPositionCard } from '../../components/LiquiPositionCard/index'
import { useUserHasLiquidityInAllTokens } from '../../data/V1'
import { StyledInternalLink, ExternalLink, TYPE, HideSmall } from '../../theme'
import { Text } from 'rebass'
import Card from '../../components/Card'
import { RowBetween, RowFixed } from '../../components/Row'
import { ButtonPrimary } from '../../components/Button'
import { AutoColumn } from '../../components/Column'
import { useActiveWeb3React } from '../../hooks'
import { CardSection, DataCard, CardNoise, CardBGImage } from '../../components/earn/styled'
import { useTomiQueryContract, useTomiPool, useTomiLP } from 'hooks/useContract'
import { calculatePercentage, convertBigNumberToNormal } from '../../utils/bigNumber-util'
import { Liquidity } from '../../model/liquidity.js'
import { isTransactionRecent, useAllTransactions } from '../../../src/state/transactions/hooks'
import { TransactionDetails } from '../../../src/state/transactions/reducer'
import { TomiStorage } from '../../model/storage.js'
import _ from 'lodash'
import { useTomiReward } from '../../hooks/useLpReward'
import { isMobile } from 'react-device-detect'
import { formatEther, formatUnits, parseUnits } from 'ethers/lib/utils'
import { useTomiConfigContract } from 'hooks/useContract'
import { BigNumber, ethers } from 'ethers'
import { formatDecimal } from 'utils/util'
import connectToTomiConfigSC from 'utils/readContract'
const PageWrapper = styled(AutoColumn)`
  max-width: 640px;
  width: 100%;
`

const VoteCard = styled(DataCard)`
  overflow: hidden;
`

const TitleRow = styled(RowBetween)`
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-wrap: wrap;
    gap: 12px;
    width: 100%;
    flex-direction: column-reverse;
  `};
`

const ButtonRow = styled(RowFixed)`
  gap: 8px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    width: 100%;
    flex-direction: row-reverse;
    justify-content: space-between;
  `};
`

const ResponsiveButtonPrimary = styled(ButtonPrimary)`
  width: fit-content;
  color: ${({ theme }) => theme.bgHeader};
  padding: 7px 25px;
  font-weight: 400;
  line-height: 20px;
  border-radius: 8px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    width: 48%;
  `};
`
const NamePool = styled(Text)`
  margin-top: 0.5rem;
  justify-self: flex-start;
  font-weight: 600;
  font-size: 18px;
  ${({ theme }) => theme.mediaWidth.upToMedium`
    font-size: 16px;
`};
  ${({ theme }) => theme.mediaWidth.upToSmall`
    width: 100%;
    display: block;
  `};
`

const EmptyProposals = styled.div`
  border: 1px solid ${({ theme }) => theme.text4};
  padding: 16px 12px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`
function newTransactionsFirst(a: TransactionDetails, b: TransactionDetails) {
  return b.addedTime - a.addedTime
}

export default function Pool() {
  const allTransactions = useAllTransactions()

  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions)
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst)
  }, [allTransactions])

  const pending = sortedRecentTransactions.filter(tx => !tx.receipt).map(tx => tx.hash)
  const tomiConfigContract = useTomiConfigContract()

  const pendingTransactions = pending.length

  const theme = useContext(ThemeContext)
  const { account, chainId } = useActiveWeb3React()

  const [amountCard, setAmountCard] = useState<number[]>([])
  const [currentAmount, setCurrentAmount] = useState<any>()

  const checkAmountCard = () => {
    setAmountCard([...amountCard, 1])
  }

  const [liquidityListAll, setLiquidityListAll] = useState<any>([])

  const hasV1Liquidity = useUserHasLiquidityInAllTokens()

  const stakingContract = useTomiQueryContract()

  const useTomiPoolQuery = useTomiPool()

  const getListToken = async () => {
    try {
      const queryTokenList = await stakingContract?.queryTokenList()
      const tokenList = queryTokenList?.map((tokenItem: any) => {
        return { address: tokenItem['tokenAddress'], decimals: tokenItem['decimal'] }
      })
      TomiStorage.setupTokenList(tokenList)
    } catch {
      console.log('something went wrong')
    }
  }

  const getLiquidityList = useCallback(async () => {
    if (!stakingContract) return
    // if (TokenList.length > 0) return

    try {
      let response = await stakingContract?.queryLiquidityList()

      const liquidityList: any[] = []
      const addressList: any[] = []
      const pairList: any[] = []

      for (let i = 0; i < response.length; i++) {
        const l = response[i]
        const address = l['lp']
        const pair = l['pair']
        const balance = l['balance'].toString()
        const totalSupply = l['totalSupply'].toString()
        const percentage = calculatePercentage(balance, totalSupply)
        const liquidity = new Liquidity(address, pair, totalSupply, parseFloat(percentage), balance)
        liquidity.liquidityShow = convertBigNumberToNormal(balance)
        liquidity.totalSupply = totalSupply
        liquidityList.push(liquidity)
        addressList.push(address)
        pairList.push(pair)
      }
      response = await stakingContract?.queryPairListInfo(pairList)

      for (let i = 0; i < pairList.length; i++) {
        const token0 = response.token0_list[i]

        const token1 = response.token1_list[i]

        liquidityList[i].setTokenAddress(token0, token1)

        const reserve0 = convertBigNumberToNormal(response.reserve0_list[i]._hex, TomiStorage.getDecimal(token0))

        const reserve1 = convertBigNumberToNormal(response.reserve1_list[i]._hex, TomiStorage.getDecimal(token1))

        liquidityList[i].setReserve(reserve0, reserve1)
      }
      setLiquidityListAll(liquidityList)
      TomiStorage.setupLiquidityList(liquidityList)
    } catch {
      console.log('something went wrong')
    }
  }, [account, chainId])

  useEffect(() => {
    getListToken()
    setLiquidityListAll([])
    setAmountCard([])
    getLiquidityList()
  }, [pendingTransactions, account, chainId])

  const [openMoreDetail, setOpenMoreDetail] = useState<number>(-1)
  const handleClickCard = (index: number) => {
    setOpenMoreDetail(index)
  }

  useEffect(() => {
    const getConfigContract = async () => {
      try {
        const resultConfig = await connectToTomiConfigSC('SWAP_FEE_PERCENT', chainId)
        setCurrentAmount(resultConfig ? formatUnits(resultConfig['value'].toString(), 2) : undefined)
      } catch {}
    }
    getConfigContract()
  }, [chainId])

  return (
    <>
      <PageWrapper>
        <SwapPoolTabs active={'pool'} />
        <VoteCard>
          <CardBGImage />
          <CardNoise />
          <CardSection style={{ padding: '12px 23px 14px' }}>
            <AutoColumn gap="16px">
              <RowBetween>
                <TYPE.white fontWeight={600} fontSize={18} lineHeight="34px">
                  Liquidity provider rewards
                </TYPE.white>
              </RowBetween>
              <RowBetween>
                <TYPE.white fontSize={14} fontWeight={400}>
                  {/* {`Liquidity providers earn a ${formatDecimal(
                    currentAmount
                  )}% fee on all trades proportional to their share of the pool. Fees are added to the pool, accrue in real time and can be claimed by withdrawing your liquidity.`} */}
                  {`Liquidity providers earn a 0.2% fee on all trades proportional to their share of the pool. Fees are added to the pool, accrue in real time and can be claimed by withdrawing your liquidity.`}
                </TYPE.white>
              </RowBetween>
              <ExternalLink style={{ textDecoration: 'underline' }} target="_blank" href="#">
                <TYPE.yellow fontWeight={400} fontSize={14}>
                  Read more about providing liquidity.
                </TYPE.yellow>
              </ExternalLink>
            </AutoColumn>
          </CardSection>
          <CardBGImage />
          <CardNoise />
        </VoteCard>

        <AutoColumn gap="lg" justify="center">
          <AutoColumn gap="lg" style={{ width: '100%' }}>
            <TitleRow style={{ marginTop: '1rem' }} padding={'0'}>
              <NamePool lineHeight={'34px'}>Your GalaxySwap liquidity</NamePool>
              <ButtonRow>
                <ResponsiveButtonPrimary
                  id="join-pool-button"
                  as={Link}
                  // borderRadius="12px"
                  to="/add/ETH"
                >
                  <Text fontWeight={600} fontSize={isMobile ? 12 : 16} minWidth={80}>
                    Add Liquidity
                  </Text>
                </ResponsiveButtonPrimary>
              </ButtonRow>
            </TitleRow>

            {!account ? (
              <Card padding="40px">
                <TYPE.body color={theme.text1} textAlign="center" fontWeight="600" lineHeight="24px">
                  Connect to a wallet to view your liquidity
                </TYPE.body>
              </Card>
            ) : (
              <>
                {liquidityListAll
                  .slice(0)
                  .reverse()
                  ?.map((v2Pair: any, index: number) => (
                    <LiquiPositionCard
                      key={`LiquiRow_${index}`}
                      checkAmountCard={checkAmountCard}
                      pair={v2Pair}
                      idx={index}
                      showDetail={index === openMoreDetail}
                      handleClickCard={handleClickCard}
                      chainId={chainId}
                    />
                  ))}
                {amountCard.length === 0 && (
                  <Card padding="40px">
                    <TYPE.body color={theme.text1} textAlign="center" fontWeight="600" lineHeight="24px">
                      Your liquidity positions will appear here.
                    </TYPE.body>
                  </Card>
                )}
              </>
            )}

            <AutoColumn justify={'center'} gap="md" style={{ display: 'none' }}>
              <Text textAlign="center" fontSize={14} style={{ padding: '.5rem 0 .5rem 0' }}>
                {hasV1Liquidity ? 'Uniswap V1 liquidity found!' : "Don't see a pool you joined?"}{' '}
                <StyledInternalLink id="import-pool-link" to={hasV1Liquidity ? '/migrate/v1' : '/find'}>
                  {hasV1Liquidity ? 'Migrate now.' : 'Import it.'}
                </StyledInternalLink>
              </Text>
            </AutoColumn>
          </AutoColumn>
        </AutoColumn>
      </PageWrapper>
    </>
  )
}
