const InfoLink = styled(ExternalLink)`
  width: 100%;
  border: 1px solid ${({ theme }) => theme.bg3};
  padding: 6px 6px;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
  color: ${({ theme }) => theme.text1};
`

const YellowLine = styled.div`
  width: 100%;
  margin-top: 20px;
  background-color: ${({ theme }) => theme.primary1};
  height: 1px;
`
export const getTextCoin = (symbol?: string, chainId?: any) => {
  if (!symbol || !chainId) return '--'
  if (symbol !== 'BNB') return symbol
  else {
    if ([56].includes(Number(chainId))) {
      return 'BNB'
    } else {
      return 'MATIC'
    }
  }
}
function TradeSummary({ trade, allowedSlippage }: { trade: Trade; allowedSlippage: number }) {
  const theme = useContext(ThemeContext)
  const { chainId } = useWeb3React()
  const isExactIn = trade.tradeType === TradeType.EXACT_INPUT
  const slippageAdjustedAmounts = computeSlippageAdjustedAmounts(trade, allowedSlippage)
  const swapProviderFee = useTomiConfig(ConfigKey.providerSwapFeeKey)
  const displaySwapProviderFee = useMemo(() => {
    return new BigNumber(swapProviderFee).div(100).toString()
  }, [swapProviderFee])
  const { priceImpactWithoutFee, realizedLPFee } = computeTradePriceBreakdown(trade, swapProviderFee)

  const outputDecimals = trade.outputAmount.currency.decimals
  const inputDecimals = trade.inputAmount.currency.decimals

  return (
    <>
      <AutoColumn style={{ padding: '0 16px' }}>
        <RowBetween>
          <RowFixed>
            <TYPE.white fontSize={16} fontWeight={400}>
              {isExactIn ? 'Minimum received' : 'Maximum sold'}
            </TYPE.white>
            <QuestionHelper text="Your transaction will revert if there is a large, unfavorable price movement before it is confirmed." />
          </RowFixed>
          <RowFixed>
            <TYPE.white color={theme.text1} fontSize={16}>
              {isExactIn
                ? `${slippageAdjustedAmounts[Field.OUTPUT]?.toFixed(
                    outputDecimals >= 8 ? 8 : outputDecimals
                  )} ${getTextCoin(trade.outputAmount.currency.symbol, chainId)}` ?? '-'
                : `${slippageAdjustedAmounts[Field.INPUT]?.toFixed(
                    inputDecimals >= 8 ? 8 : inputDecimals
                  )} ${getTextCoin(trade.inputAmount.currency.symbol, chainId)}` ?? '-'}
            </TYPE.white>
          </RowFixed>
        </RowBetween>
        {/* <RowBetween style={{ marginTop: '20px' }}>
          <RowFixed>
            <TYPE.white fontSize={16} fontWeight={400}>
              Price Impact
            </TYPE.white>
            <QuestionHelper text="The difference between the market price and estimated price due to trade size." />
          </RowFixed>
          <FormattedPriceImpact priceImpact={priceImpactWithoutFee} />
        </RowBetween> */}

        {/* <RowBetween style={{ marginTop: '20px' }}>
          <RowFixed>
            <TYPE.white fontSize={16} fontWeight={400}>
              Liquidity Provider Fee
            </TYPE.white>
            <QuestionHelper
              // text={`A portion of each trade (${displaySwapProviderFee}%) goes to liquidity providers as a protocol incentive.`}
              text={`A portion of each trade (0.2%) goes to liquidity providers as a protocol incentive.`}
            />
          </RowFixed>
          <TYPE.white fontSize={16} color={theme.text1}>
            {realizedLPFee
              ? realizedLPFee.lessThan(MIN_CURRENCY)
                ? '<0.01' + ' ' + getTextCoin(trade.inputAmount.currency.symbol, chainId)
                : realizedLPFee?.toFixed(6) + ' ' + getTextCoin(trade.inputAmount.currency.symbol, chainId)
              : '-'}
          </TYPE.white>
        </RowBetween> */}

        <YellowLine />
      </AutoColumn>
    </>
  )
}

