import { Trade, TradeType } from 'tomiswap-sdk'
import React, { useContext, useMemo } from 'react'
import { ArrowDown, AlertTriangle, ArrowRight } from 'react-feather'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { Field } from '../../state/swap/actions'
import { TYPE } from '../../theme'
import { ButtonPrimary } from '../Button'
import { isAddress, shortenAddress } from '../../utils'
import { computeSlippageAdjustedAmounts, computeTradePriceBreakdown, warningSeverity } from '../../utils/prices'
import { AutoColumn } from '../Column'
import CurrencyLogo from '../CurrencyLogo'
import { RowBetween, RowFixed } from '../Row'
import { TruncatedText, SwapShowAcceptChanges } from './styleds'
import { getTextCoin } from './AdvancedSwapDetails'
import { useWeb3React } from '@web3-react/core'

export default function SwapModalHeader({
  trade,
  allowedSlippage,
  recipient,
  showAcceptChanges,
  onAcceptChanges
}: {
  trade: Trade
  allowedSlippage: number
  recipient: string | null
  showAcceptChanges: boolean
  onAcceptChanges: () => void
}) {
  const slippageAdjustedAmounts = useMemo(() => computeSlippageAdjustedAmounts(trade, allowedSlippage), [
    trade,
    allowedSlippage
  ])
  const { priceImpactWithoutFee } = useMemo(() => computeTradePriceBreakdown(trade), [trade])
  const priceImpactSeverity = warningSeverity(priceImpactWithoutFee)
  const { chainId } = useWeb3React()
  const theme = useContext(ThemeContext)

  return (
    <AutoColumn gap={'md'} style={{ marginTop: '20px' }}>
      <RowBetween>
        <RowFixed width={'45%'} flexFlow={'column'} align="flex-start">
          <RowFixed gap={'0 0 10px 0'}>
            <CurrencyLogo currency={trade.inputAmount.currency} size={'50px'} />
          </RowFixed>
          <RowFixed gap={'0px'}>
            <TruncatedText
              fontSize={14}
              fontWeight={500}
              color={showAcceptChanges && trade.tradeType === TradeType.EXACT_OUTPUT ? theme.primary1 : ''}
            >
              {trade.inputAmount.toSignificant(6)}
            </TruncatedText>
            <Text fontSize={14} fontWeight={500} style={{ marginLeft: '10px' }}>
              {getTextCoin(trade.inputAmount.currency.symbol, chainId)}
            </Text>
          </RowFixed>
        </RowFixed>
        <RowFixed>
          <ArrowRight size="24" color={theme.text1} style={{ minWidth: '24px' }} />
        </RowFixed>
        <RowFixed width={'45%'} flexFlow={'column'} align="flex-end">
          <RowFixed gap={'0 0 10px 0'}>
            <CurrencyLogo currency={trade.outputAmount.currency} size={'50px'} />
          </RowFixed>
          <RowFixed gap={'0px'}>
            <TruncatedText
              fontSize={14}
              fontWeight={500}
              color={
                priceImpactSeverity > 3
                  ? theme.red1
                  : showAcceptChanges && trade.tradeType === TradeType.EXACT_INPUT
                  ? theme.primary1
                  : ''
              }
            >
              {trade.outputAmount.toSignificant(6)}
            </TruncatedText>
            <Text fontSize={14} fontWeight={500} style={{ marginLeft: '10px' }}>
              {getTextCoin(trade.outputAmount.currency.symbol, chainId)}
            </Text>
          </RowFixed>
        </RowFixed>
      </RowBetween>

      {showAcceptChanges ? (
        <SwapShowAcceptChanges justify="flex-start" gap={'0px'}>
          <RowBetween>
            <RowFixed>
              <AlertTriangle size={20} style={{ marginRight: '8px', minWidth: 24 }} />
              <TYPE.main color={theme.primary1}> Price Updated</TYPE.main>
            </RowFixed>
            <ButtonPrimary
              style={{ padding: '.5rem', width: 'fit-content', fontSize: '0.825rem', borderRadius: '12px' }}
              onClick={onAcceptChanges}
            >
              Accept
            </ButtonPrimary>
          </RowBetween>
        </SwapShowAcceptChanges>
      ) : null}
      <AutoColumn color={theme.primary1} justify="flex-start" gap="sm" style={{ padding: '0 0 0 0px' }}>
        {trade.tradeType === TradeType.EXACT_INPUT ? (
          <TYPE.swapConfirm fontSize={14} textAlign="left" style={{ width: '100%' }}>
            {`Output is estimated. You will receive at least ` +
              slippageAdjustedAmounts[Field.OUTPUT]?.toSignificant(6) +
              ' ' +
              getTextCoin(trade.outputAmount.currency.symbol, chainId) +
              ' or the transaction will revert.'}
          </TYPE.swapConfirm>
        ) : (
          <TYPE.swapConfirm fontSize={14} textAlign="left" style={{ width: '100%' }}>
            {`Input is estimated. You will sell at most ` +
              slippageAdjustedAmounts[Field.INPUT]?.toSignificant(6) +
              ' ' +
              getTextCoin(trade.inputAmount.currency.symbol, chainId) +
              ' or the transaction will revert.'}
          </TYPE.swapConfirm>
        )}
      </AutoColumn>
      {recipient !== null ? (
        <AutoColumn justify="flex-start" gap="sm" style={{ padding: '12px 0 0 0px' }}>
          <TYPE.main>
            Output will be sent to{' '}
            <b title={recipient}>{isAddress(recipient) ? shortenAddress(recipient) : recipient}</b>
          </TYPE.main>
        </AutoColumn>
      ) : null}
    </AutoColumn>
  )
}
