import React, { useCallback, useEffect, useState } from 'react'

import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { AutoRow, RowBetween, RowFixed } from '../Row'
import { TYPE } from '../../theme'
import { X } from 'react-feather'
import { ButtonConfirmed, ButtonEmpty, ButtonPrimary } from '../Button'
import { escapeRegExp } from '../../utils'
import { formatDecimal } from 'utils/util'
import { handleInputChange } from 'react-select/src/utils'
import { parseEther } from 'ethers/lib/utils'
import { useBallotContract, useGovernanceContract, useTomiTokenContract } from 'hooks/useContract'
import { useActiveWeb3React } from 'hooks'
import { MaxUint256 } from '@uniswap/sdk-core'
import Loader from 'components/Loader'
import { TOMI_GOVERNANCE } from 'constants/addresses'
import { useTransactionAdder } from 'state/transactions/hooks'
import { isMobile } from 'react-device-detect'
interface VoteDepositModalProps {
  isOpen: boolean
  onDismiss: () => void
  onHandle: (e: any, amount?: any) => void
  balance: string
  ballot: string
  ballotId: string
  setIsOpenRejected: (e: boolean) => void
}
const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 24px;
`
const StyledClosed = styled(X)`
  :hover {
    cursor: pointer;
  }
`
const TextError = styled.p`
  color: ${({ theme }) => theme.red2};
  margin-top: 0.5rem;
  font-size: 16px;
`
const InputDiv = styled.div`
  background: #212121;
  width: 100%;
  height: 51px;
  /* strocke 1 */

  border: 1px solid #e01286;
  box-sizing: border-box;
  border-radius: 8px;
  color: #fffffff;
  #input-box {
    color: #ffffff;
  }
`
const InputBox = styled.input`
  background-color: transparent;
  width: ${isMobile ? '50%' : '60%'};
  height: 100%;
  font-size: 16px;
  border: none;
  color: #fffffff !important;
  padding-left: 1rem;
  :focus {
    outline: none;
  }
`
const MaxButton = styled.button`
  align-items: center;
  letter-spacing: 0.75px;
  background-color: transparent;
  left: ${isMobile ? 'calc(50% - 42px / 2 + 109.5px)' : 'calc(50% - 42px / 2 + 151.5px)'};
  border: none;
  color: #e01286;
  cursor: pointer;
`
const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`) // match escaped "." characters via in a non-capturing group

export default function VoteDepositModal({
  isOpen,
  onDismiss,
  onHandle,
  balance,
  ballot,
  ballotId,
  setIsOpenRejected
}: VoteDepositModalProps) {
  const [amount, setAmount] = useState('')
  const [approved, setApproved] = useState(false)
  const [notInput, setNotInput] = useState(true)
  const { chainId, account } = useActiveWeb3React()
  const [approveLoading, setApproveLoading] = useState(false)
  const tomiTokenContract = useTomiTokenContract()
  const governanceContract = useGovernanceContract()
  const getStaticInfo = useCallback(async () => {
    try {
      if (tomiTokenContract && chainId) {
        const allowance = await tomiTokenContract?.functions.allowance(account, TOMI_GOVERNANCE[chainId])
        setApproved(allowance[0].gt(0))
      }
    } catch (e) {
      console.log('something went wrong: ', e)
    }
  }, [tomiTokenContract])

  useEffect(() => {
    getStaticInfo()
  }, [])
  const enforcer = (nextUserInput: string) => {
    if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
      if (nextUserInput.includes('.')) {
        const arr = nextUserInput.split('.')
        const decimal = arr[1]
        if (decimal.length > 8) return
      }
      if (notInput) setNotInput(false)
      setAmount(nextUserInput)
    }
  }
  const setMax = () => {
    setAmount(
      formatDecimal((Number(balance) + Number(ballot)).toString())
        .toString()
        .replace(',', '')
    )
  }
  const handle = () => {
    if (amount == '0' || Number(amount) <= Number(ballot)) {
      const diff = Number(amount)
      onHandle(parseEther(ballot).toString(), parseEther(diff.toString()).toString())
    } else {
      const diff = Number(amount)
      onHandle(parseEther(diff.toString()).toString())
    }
  }
  const addTransaction = useTransactionAdder()
  const handleApprove = async () => {
    setApproveLoading(true)
    try {
      if (chainId && tomiTokenContract) {
        const transaction = await tomiTokenContract?.approve(TOMI_GOVERNANCE[chainId], MaxUint256.toString())
        addTransaction(transaction, {
          summary: `Approve ${amount} TOMI`
        })
        const result = await transaction.wait()
        getStaticInfo()
      }
    } catch (e) {
      setIsOpenRejected(true)
      console.log(e)
    }
    setApproveLoading(false)
  }

  const handleDismiss = () => {
    setAmount('')
    setNotInput(true)
    onDismiss()
  }

  const needToDeposit = !(amount == '0' || Number(amount) <= Number(ballot))
  const isError = amount ? Number(amount) > Number(balance) + Number(ballot) : false
  const isEmpty = !amount && !notInput ? true : false
  const realAmount = amount == '0' || Number(amount) <= Number(ballot) ? ballot : Number(amount)
  const realAmountZero = realAmount == 0
  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <ContentWrapper gap="lg">
        <AutoColumn gap="lg" justify="center">
          <RowBetween>
            <TYPE.mediumHeader fontWeight={400}>Vote</TYPE.mediumHeader>
            <StyledClosed stroke="white" onClick={handleDismiss} />
          </RowBetween>
          <RowBetween>
            <InputDiv
              style={{ border: `1px solid ${isError || isEmpty || (amount && realAmountZero) ? 'red' : '#e01286'}` }}
            >
              <InputBox
                id="input-box"
                inputMode="decimal"
                type="text"
                pattern="^[0-9]*[.,]?[0-9]*$"
                placeholder={'Amount'}
                value={amount}
                minLength={1}
                maxLength={79}
                onChange={event => {
                  enforcer(event.target.value.replace(/,/g, '.'))
                }}
              />
              <span style={{ position: 'absolute', marginTop: '13px', marginRight: '1rem' }}>TOMI point</span>
              <MaxButton style={{ position: 'absolute', marginTop: '13px', fontSize: '16px' }} onClick={setMax}>
                MAX
              </MaxButton>
              {isError && <TextError>Insufficient balance</TextError>}
              {isEmpty && <TextError>Please enter an amount</TextError>}
              {amount && realAmountZero && <TextError>Ballot vote much be greater than 0</TextError>}
            </InputDiv>
          </RowBetween>
          <RowFixed style={{ marginTop: '6px' }}>
            If your input amount smaller than your TOMI point, all of your TOMI point is going to vote
          </RowFixed>
          <RowBetween>Wallet balance: {formatDecimal(balance)} TOMI</RowBetween>
          <RowBetween>Your TOMI point: {formatDecimal(ballot)}</RowBetween>
          <RowBetween>
            {needToDeposit ? (
              <>
                {!approved ? (
                  <ButtonConfirmed
                    disabled={approveLoading || isError}
                    style={{ width: '100%%', fontSize: '18px' }}
                    onClick={() => handleApprove()}
                  >
                    {approveLoading ? (
                      <AutoRow gap="6px" justify="center">
                        Approving <Loader stroke="white" />
                      </AutoRow>
                    ) : (
                      <>Approve</>
                    )}
                  </ButtonConfirmed>
                ) : (
                  <>
                    <ButtonEmpty
                      style={{ width: '46%', color: 'white', height: '52px' }}
                      onClick={() => handleDismiss()}
                    >
                      <TYPE.mediumHeader fontSize={16} color="white">{`Exit`}</TYPE.mediumHeader>
                    </ButtonEmpty>
                    <ButtonPrimary
                      style={{ width: '46%' }}
                      disabled={isError || amount == '' || realAmountZero}
                      onClick={() => handle()}
                    >
                      Confirm
                    </ButtonPrimary>
                  </>
                )}
              </>
            ) : (
              <>
                <ButtonEmpty style={{ width: '46%', color: 'white', height: '52px' }} onClick={() => handleDismiss()}>
                  <TYPE.mediumHeader fontSize={16} color="white">{`Exit`}</TYPE.mediumHeader>
                </ButtonEmpty>
                <ButtonPrimary
                  style={{ width: '46%' }}
                  disabled={isError || amount == '' || realAmountZero}
                  onClick={() => handle()}
                >
                  Confirm
                </ButtonPrimary>
              </>
            )}
          </RowBetween>
        </AutoColumn>
      </ContentWrapper>
    </Modal>
  )
}
