const convertToNumberDecimals0 = (num: string) => {
  const sliceNum = num
    .slice(num.length - 8)
    .split('')
    .reverse()

  let countZero = 0
  for (const element of sliceNum) {
    if (element !== '0') {
      break
    } else {
      countZero++
    }
  }
  const result = sliceNum.slice(countZero)
  return result.length
}

export const convertToNumberDecimals = (amount?: any, decimals?: number) => {
  const decimal = decimals !== undefined ? decimals : 2
  const decimalToFixed = decimal > 8 ? 8 : decimal
  if (amount === undefined || amount.toString() === 'NaN') {
    return undefined
  } else {
    return decimal === 0
      ? truncateString(
          amount?.toFixed(convertToNumberDecimals0(truncateString(amount?.toFixed(9), 8)) + 1),
          convertToNumberDecimals0(truncateString(amount?.toFixed(9), 8))
        )
      : truncateString(amount?.toFixed(decimalToFixed + 1), decimalToFixed)
  }
}
