import { useTomiLP } from './useContract'
import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { TransactionResponse } from '@ethersproject/providers'
import { useHasTransactionSuccess, useIsTransactionPending, useTransactionAdder } from '../state/transactions/hooks'
import { ApprovalState } from './useApproveCallback'

export function useTomiReward(address: string, index: number) {
  const [reward, setReward] = useState('0')
  const contract = useTomiLP(address)

  useEffect(() => {
    ;(async function() {
      if (address) {
        const res = await contract?.queryReward()
        res ? setReward(new BigNumber(res._hex).div(new BigNumber(10).pow(18)).toFixed(6)) : setReward('0')
      }
    })()
  }, [address, index])

  return reward
}

export function useMintRewardCallback(address: string, callbackSuccess: Function): [boolean, () => Promise<void>] {
  const lpContract = useTomiLP(address)
  const addTransaction = useTransactionAdder()

  const [txSuccess, setTxSuccess] = useState('')

  const success = useHasTransactionSuccess(txSuccess)
  const isPendingTransaction = useIsTransactionPending(txSuccess)
  useEffect(() => {
    if (success) {
      callbackSuccess()
      setTxSuccess('')
    }
  }, [success])

  const mintReward = useCallback(async () => {
    if (isPendingTransaction) {
      console.error('claiming')
      return
    }

    if (!lpContract) {
      console.error('lpContract is null')
      return
    }
    return lpContract
      .mintReward()
      .then((response: TransactionResponse) => {
        addTransaction(response, {
          summary: 'Mint reward LP token'
        })
        setTxSuccess(response.hash)
      })
      .catch((error: Error) => {
        console.debug('Failed to mint reward lp token', error)
        throw error
      })
  }, [lpContract, isPendingTransaction])

  return [isPendingTransaction, mintReward]
}
