import { Trade } from 'tomiswap-sdk'
import { useMemo } from 'react'
import { TOMI, ZERO_ADDRESS } from '../constants'
import { useActiveWeb3React } from './index'
import { useFactoryContract } from './useContract'
import { useSingleContractMultipleData } from '../state/multicall/hooks'

export function useCheckPoolWithTomi(trade?: Trade) {
  const { chainId } = useActiveWeb3React()
  const [tokenAddresses] = useMemo(() => {
    const dataTokens: any = []
    trade?.route.path.map(token => {
      if (chainId && !token.equals(TOMI[chainId])) {
        dataTokens.push([token.address, TOMI[chainId].address])
      }
    })
    // console.log('dataTokens', dataTokens)
    return [dataTokens]
  }, [trade])

  const factory = useFactoryContract()
  const dataPairAddresses = useSingleContractMultipleData(factory, 'getPair', tokenAddresses)

  const tokensNoPairWithTomi = useMemo(() => {
    return dataPairAddresses.map(({ result }, index) => result?.[0] === ZERO_ADDRESS && trade?.route.path[index])
  }, [dataPairAddresses])

  return tokensNoPairWithTomi.filter(e => e !== false)
}
