import { useSingleCallResult } from '../state/multicall/hooks'
import { useTomiConfigContract } from './useContract'
import BigNumber from 'bignumber.js'

export enum ConfigKey {
  providerSwapFeeKey = '0x535741505f4645455f50455243454e5400000000000000000000000000000000'
}

//30/100
const BASE_FEE = '30'

export function useTomiConfig(key: ConfigKey): string {
  const tomiConfigContract = useTomiConfigContract()
  const result = useSingleCallResult(tomiConfigContract, 'getConfigValue', [key]).result
  return result ? result.toString() : BASE_FEE
}
