import React, { useEffect, useState } from 'react'

import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { RowBetween, RowFixed } from '../Row'
import { TYPE } from '../../theme'
import { X } from 'react-feather'
import { ButtonConfirmed, ButtonEmpty, ButtonPrimary } from '../Button'
import { escapeRegExp } from '../../utils'
import { formatDecimal } from 'utils/util'
import { isMobile } from 'react-device-detect'

interface RedeemModalProps {
  isOpen: boolean
  onDismiss: () => void
  onHandle: (e: any) => void
  balance: string
}
const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 24px;
`
const StyledClosed = styled(X)`
  :hover {
    cursor: pointer;
  }
`
const TextError = styled.p`
  color: ${({ theme }) => theme.red2};
  margin-top: 0.5rem;
  font-size: 16px;
`
const InputDiv = styled.div`
  background: #212121;
  width: 100%;
  height: 51px;
  /* strocke 1 */

  border: 1px solid #e01286;
  box-sizing: border-box;
  border-radius: 8px;
  color: #fffffff;
  #input-box {
    color: #ffffff;
  }
`
const InputBox = styled.input`
  background-color: transparent;
  width: ${isMobile ? '50%' : '60%'};
  height: 100%;
  border: none;
  font-size: 16px;
  color: #fffffff !important;
  padding-left: 1rem;
  :focus {
    outline: none;
  }
  ::-webkit-search-decoration {
    -webkit-appearance: none;
  }

  [type='number'] {
    -moz-appearance: textfield;
  }
`
const MaxButton = styled.button`
  align-items: center;
  letter-spacing: 0.75px;
  background-color: transparent;
  left: ${isMobile ? 'calc(50% - 42px / 2 + 109.5px)' : 'calc(50% - 42px / 2 + 151.5px)'};
  border: none;
  color: #e01286;
  cursor: pointer;
`
const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`) // match escaped "." characters via in a non-capturing group

export default function PoolModal({ isOpen, onDismiss, onHandle, balance }: RedeemModalProps) {
  const [amount, setAmount] = useState('')
  const [notInput, setNotInput] = useState(true)
  const enforcer = (nextUserInput: string) => {
    if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
      if (nextUserInput.includes('.')) {
        const arr = nextUserInput.split('.')
        const decimal = arr[1]
        if (decimal.length > 8) return
      }
      if (notInput) setNotInput(false)
      setAmount(nextUserInput)
    }
  }
  const setMax = () => {
    setAmount(
      formatDecimal(balance)
        .toString()
        .replace(',', '')
    )
  }
  const handleDismiss = () => {
    setAmount('')
    setNotInput(true)
    onDismiss()
  }

  const isError = amount ? Number(amount) > Number(balance) : false
  const isZero = amount ? amount == '0' : false
  const isEmpty = !amount && !notInput ? true : false
  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <ContentWrapper gap="lg">
        <AutoColumn gap="lg" justify="center">
          <RowBetween>
            <TYPE.mediumHeader fontWeight={600} fontSize={18}>
              Collect TOMI Earned
            </TYPE.mediumHeader>
            <StyledClosed stroke="white" onClick={onDismiss} />
          </RowBetween>
          <RowFixed>
            <h1>{formatDecimal(balance)}</h1>
          </RowFixed>
          <RowBetween>
            <ButtonEmpty
              style={{ textDecoration: 'none', width: '46%', color: 'white', height: '52px' }}
              onClick={() => handleDismiss()}
            >
              <TYPE.mediumHeader fontSize="16px" color="white">{`Exit`}</TYPE.mediumHeader>
            </ButtonEmpty>
            <ButtonPrimary style={{ width: '46%' }} disabled={Number(balance) == 0} onClick={() => onHandle(amount)}>
              Confirm
            </ButtonPrimary>
          </RowBetween>
        </AutoColumn>
      </ContentWrapper>
    </Modal>
  )
}
