export class Liquidity {
  constructor(address, pair, totalSupply, percent, liquidity, liquidityShow, reward) {
    this.address = address
    this.pair = pair
    this.totalSupply = totalSupply
    this.percent = percent
    this.liquidity = liquidity
    this.liquidityShow = liquidityShow
    this.reward = reward
  }

  setTokenAddress(token0, token1) {
    this.token0 = token0
    this.token1 = token1
  }

  setReserve(reserve0, reserve1) {
    this.reserve0 = reserve0
    this.reserve1 = reserve1
  }
  setDecimal(decimal0, decimal1) {
    this.decimal0 = decimal0
    this.decimal1 = decimal1
  }

  setReward(reward) {
    this.reward = reward
  }

  setApr(fee, mint) {
    this.aprFee = fee
    this.aprMining = mint
  }
}
