import React, { memo } from 'react'
import CheckIcon from '../icons/CheckedIcon'
import UncheckIcon from '../icons/UncheckIcon'

interface Props {
  checked: boolean
  onChange: () => void
}

const Checkbox = (props: Props) => {
  const { checked, onChange } = props

  return (
    <div onClick={onChange} style={{ alignItems: 'center' }}>
      {checked ? <CheckIcon /> : <UncheckIcon />}
    </div>
  )
}

export default memo(Checkbox)
