import { ApolloClient } from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { HttpLink } from 'apollo-link-http'
import { TOMI_TOKEN } from 'constants/addresses'
import { ChainId } from 'tomiswap-sdk'
import { gql } from '@apollo/client'

const REACT_APP_TOMI_URL_SUBGRAPH = process.env.REACT_APP_TOMI_URL_SUBGRAPH
const REACT_APP_TOMI_URL_SUBGRAPH_POLYGON = process.env.REACT_APP_TOMI_URL_SUBGRAPH_POLYGON

export const client = new ApolloClient({
  link: new HttpLink({
    uri: REACT_APP_TOMI_URL_SUBGRAPH
  }),
  cache: new InMemoryCache()
})
export const clientPolygon = new ApolloClient({
  link: new HttpLink({
    uri: REACT_APP_TOMI_URL_SUBGRAPH_POLYGON
  }),
  cache: new InMemoryCache()
})

export const TOKEN_DATA = (tokenAddress: string, block?: any) => {
  const queryString = `
  fragment TokenFields on Token {
    id
    name
    symbol
    derivedETH
    tradeVolume
    tradeVolumeUSD
    untrackedVolumeUSD
    totalLiquidity
    txCount
  }

    query tokens {
      tokens(block : {number: ${block}} where: {id:"0xe774238f5ef7ea239be157d4adb82bc753b36836"}) {
        ...TokenFields
      }
    }
  `
  return gql(queryString)
}
const BUNDLE_ID = 1
export const ETH_PRICE = (block?: any) => {
  const queryString = block
    ? `
    query bundles {
      bundles(where: { id: ${BUNDLE_ID} } block: {number: ${block}}) {
        id
        ethPrice
      }
    }
  `
    : ` query bundles {
      bundles(where: { id: ${BUNDLE_ID} }) {
        id
        ethPrice
      }
    }
  `
  return gql(queryString)
}
export const getTomiPrice = async (chainId: ChainId, blockNumber: any) => {
  try {
    const result = await client.query({
      query: TOKEN_DATA(TOMI_TOKEN[chainId], blockNumber)
    })
    const derivedETH = result?.data?.tokens?.[0].derivedETH

    const priceEthResult = await client.query({
      query: ETH_PRICE(),
      fetchPolicy: 'cache-first'
    })
    const priceEth = priceEthResult?.data?.bundles[0].ethPrice
    return priceEth * derivedETH
  } catch (e) {
    return 0
  }
}

export const getTransaction = async (account: any, chainId: any) => {
  try {
    const query = `
    query stakeEvents {
      stakeEvents(where: { address: "${account}" }) {
        id,
        type,
        address,
        date,
        value
      }
    }
    `
    const finalClient = [1, 2, 3, 4, 5].includes(chainId) ? client : clientPolygon
    const priceEthResult = await finalClient.query({
      query: gql(query),
      fetchPolicy: 'no-cache'
    })
    console.log(priceEthResult)
    return priceEthResult.data.stakeEvents
  } catch (e) {
    return []
  }
}
