import React, { useContext } from 'react'
import { CheckCircle, AlertTriangle } from 'react-feather'
import styled, { ThemeContext } from 'styled-components'
import { useActiveWeb3React } from '../../hooks'
import { TYPE } from '../../theme'
import { ExternalLink } from '../../theme/components'
import { getEtherscanLink } from '../../utils'
import { AutoColumn } from '../Column'
import { AutoRow } from '../Row'

const RowNoFlex = styled(AutoRow)`
  flex-wrap: nowrap;
`

export default function TransactionPopup({
  hash,
  success,
  summary
}: {
  hash: string
  success?: boolean
  summary?: string
}) {
  const { chainId } = useActiveWeb3React()

  const theme = useContext(ThemeContext)

  return (
    <RowNoFlex>
      <div style={{ paddingRight: 16 }}>
        {success ? <CheckCircle color={theme.green1} size={24} /> : <AlertTriangle color={theme.red1} size={24} />}
      </div>
      <div style={{ width: '100%' }}>
        <div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
          <TYPE.body fontWeight={500}>
            {`${summary ?? 'Hash: ' + hash.slice(0, 8) + '...' + hash.slice(58, 65)} ${
              success ? ' success' : ' failed'
            }`}
          </TYPE.body>
        </div>
        <div style={{ width: '100%', display: 'flex', justifyContent: 'center', fontSize: '14px', marginTop: '5px' }}>
          {chainId && (
            <ExternalLink href={getEtherscanLink(chainId, hash, 'transaction')}>View on Explorer</ExternalLink>
          )}
        </div>
      </div>
    </RowNoFlex>
  )
}
