// import BigNumber from "../library/bignumber.js"
import BigNumber from 'bignumber.js'
export function convertBigNumberToNormal(bigNumber: BigNumber, decimals = 18) {
  const result = new BigNumber(bigNumber).dividedBy(new BigNumber(Math.pow(10, decimals)))
  return result.toFixed()
}
const NUMBER_OF_DECIMAL = 8
export function convertNormalToBigNumber(number: number, decimals = 18, fix = 0) {
  return new BigNumber(number)
    .multipliedBy(new BigNumber(Math.pow(10, decimals)))
    .minus(fix)
    .toFixed(0)
}

export function calculatePercentage(numerator: BigNumber, denominator: BigNumber) {
  return new BigNumber(numerator).dividedBy(new BigNumber(denominator)).toFixed()
}

export function calculateMultiplied(number1: BigNumber, number2: BigNumber) {
  return new BigNumber(number1).multipliedBy(new BigNumber(number2)).toFixed(0)
}

export function minusBigNumber(number1: BigNumber, number2: BigNumber) {
  return new BigNumber(number1).minus(new BigNumber(number2)).toFixed(0)
}

export function getDeadLine(delay: number) {
  return Math.floor(new Date().getTime() / 1000 + 60 * delay)
}
const formatAfterDecial = (number: string) => {
  if (!number) return 0
  let newNumber = number
  for (let i = number.length - 1; i >= 0; i--) {
    if (newNumber[i] === '0') newNumber = newNumber.substring(0, newNumber.length - 1)
    else {
      break
    }
  }
  return newNumber
}

export const formatDecimal = (num: string, numberDecimal = NUMBER_OF_DECIMAL) => {
  try {
    if (!num) return '0.00000000'
    // if (isNaN(num)) return num
    if (Number(num) == 0 && numberDecimal == 8) return '0.00000000'
    const numString = num.split('.')
    const beforeDecimal = numString[0].replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    if (numString.length > 1) {
      const afterDecimal = formatAfterDecial(numString[1].substring(0, numberDecimal))
      if (afterDecimal === '0' || afterDecimal === '') return beforeDecimal
      return beforeDecimal + '.' + afterDecimal
    } else {
      return beforeDecimal
    }
  } catch (e) {
    return num
  }
}
